<template>
  <div class="the-catalogue-table">
    <div class="the-catalogue-table__header">
      <div
        class="the-catalogue-table__header-column the-catalogue-table__header-name"
        @click="getFilterIndex(0)"
        :class="[
          filterParametr.value === null || filterParametr.value !== 0
            ? ''
            : filterParametr.sortOrder && filterParametr.value === 0
              ? 'the-catalogue-table__header-column_low'
              : 'the-catalogue-table__header-column_high'
        ]"
      >
        <div class="the-catalogue-table__filter">
          <e-icon class="the-catalogue-table__filter-arrow the-catalogue-table__filter-arrow_up" :src="iconArrowUp"></e-icon>
          <e-icon class="the-catalogue-table__filter-arrow the-catalogue-table__filter-arrow_down" :src="iconArrowDown"></e-icon>
        </div>
        Token Name
      </div>
      <div class="the-catalogue-table__header-column the-catalogue-table__header-price"
        @click="getFilterIndex(1)"
        :class="[
          filterParametr.value === null || filterParametr.value !== 1
            ? ''
            : filterParametr.sortOrder && filterParametr.value === 1
              ? 'the-catalogue-table__header-column_low'
              : 'the-catalogue-table__header-column_high'
          ]"
        >
        <div class="the-catalogue-table__filter">
          <e-icon class="the-catalogue-table__filter-arrow the-catalogue-table__filter-arrow_up" :src="iconArrowUp"></e-icon>
          <e-icon class="the-catalogue-table__filter-arrow the-catalogue-table__filter-arrow_down" :src="iconArrowDown"></e-icon>
        </div>
        Price
      </div>
      <div class="the-catalogue-table__header-column the-catalogue-table__header-volume"
        @click="getFilterIndex(2)"
        :class="[
            filterParametr.value === null || filterParametr.value !== 2
              ? ''
              : filterParametr.sortOrder && filterParametr.value === 2
                ? 'the-catalogue-table__header-column_low'
                : 'the-catalogue-table__header-column_high'
          ]"
        >
        <div class="the-catalogue-table__filter">
          <e-icon class="the-catalogue-table__filter-arrow the-catalogue-table__filter-arrow_up" :src="iconArrowUp"></e-icon>
          <e-icon class="the-catalogue-table__filter-arrow the-catalogue-table__filter-arrow_down" :src="iconArrowDown"></e-icon>
        </div>
        Volume 24h
      </div>
      <div class="the-catalogue-table__header-column the-catalogue-table__header-change"
        @click="getFilterIndex(3)"
        :class="[
            filterParametr.value === null || filterParametr.value !== 3
              ? ''
              : filterParametr.sortOrder && filterParametr.value === 3
                ? 'the-catalogue-table__header-column_low'
                : 'the-catalogue-table__header-column_high'
          ]"
        >
        <div class="the-catalogue-table__filter">
          <e-icon class="the-catalogue-table__filter-arrow the-catalogue-table__filter-arrow_up" :src="iconArrowUp"></e-icon>
          <e-icon class="the-catalogue-table__filter-arrow the-catalogue-table__filter-arrow_down" :src="iconArrowDown"></e-icon>
        </div>
        Marketcap
      </div>
      <div
        class="the-catalogue-table__header-column the-catalogue-table__header-graph"
        @click="getFilterIndex(4)"
          :class="[
              filterParametr.value === null || filterParametr.value !== 4
                ? ''
                : filterParametr.sortOrder && filterParametr.value === 4
                  ? 'the-catalogue-table__header-column_low'
                  : 'the-catalogue-table__header-column_high'
            ]"
      >
        <div class="the-catalogue-table__filter">
          <e-icon class="the-catalogue-table__filter-arrow the-catalogue-table__filter-arrow_up" :src="iconArrowUp"></e-icon>
          <e-icon class="the-catalogue-table__filter-arrow the-catalogue-table__filter-arrow_down" :src="iconArrowDown"></e-icon>
        </div>
        Price Change
        <!-- TODO: Как только будет дано описание, как должна работать эта выпадашка нужн будет раскомментировать -->
        <!-- <div class="the-catalogue-table__dropdown">
          <div class="the-catalogue-table__dropdown-current">
            {{priceChange}} hours
            <e-icon :src="iconArrowDown" />
          </div>
        </div> -->
      </div>
    </div>

    <e-container-scroll
      class="the-catalogue-table__content"
      ref="scroll"
    >
      <transition-list>
        <div
          v-for="(content, index) in sortBy"
          :key="content.contract"
          class="the-catalogue-table__item"
        >
          <e-catalogue-table-item
            :token-contract="content.contract"
            :token-name="content.name"
            :token-abbr="content.shortName"
            :decimals="content.decimals"
            :increasing="content.increasing24"
            :priceDiff24InPercent="content.priceDiff24InPercent"
            :price="content.price"
            :volume24="content.volume24h"
            :volume="content.total"
            :marketcap="content.marketcap"
            :isMy="content.isMy"
            :index="activePage * pageSize + index + 1"
            :websiteUrl="content.websiteUrl"
            :isWebsiteVerified="content.isWebsiteVerified"
            :popover-top="index === sortBy.length - 1"
          />
        </div>
      </transition-list>
    </e-container-scroll>
  </div>
</template>

<script>
import bnSort from '@/util/bnSort';

// Components
import ECatalogueTableItem from '@/components/ECatalogueTableItem';
import EIcon from '@/components/EIcon';
import EContainerScroll from '@/components/EContainerScroll';

// Transitions
import TransitionList from '@/containers/transitions/TransitionList';

// Icons
import iconArrowUp from '@/icons/e-icon_arrow_upper.svg';
import iconArrowDown from '@/icons/e-icon_arrow_downer.svg';


export default {
  name: 'the-catalogue-table',

  props: {
    tableParams: {
      type: Array,
      default: () => [],
    },

    // Номер активной отображаемой страницы
    activePage: {
      type: Number,
      default: 0,
    },

    // Размер страницы
    pageSize: {
      type: Number,
      default: 20,
    },
  },

  data() {
    return {
      filterState: null,
      filterParametr: {
        value: 2,
        sortOrder: false,
      },
      sortOrder: [
        true,
        true,
        false,
        true,
        true,
        true,
      ],
      priceChange: 24,
    };
  },

  created() {
    Object.assign(this, {
      iconArrowUp,
      iconArrowDown,
    });
  },

  watch: {
    // Обновление и прокручиване скрола при смене страниці
    activePage() {
      if (this.$refs.scroll) {
        this.$refs.scroll.scrollToTop();
      }
    },
  },

  computed: {
    sortBy() {
      let res = [];
      const tableData = [...this.tableParams];
      const currentIndex = this.filterParametr.value;
      const sortOrderValue = this.filterParametr.sortOrder;

      switch (currentIndex) {
        case 0:
          // sort by tokenName
          res = tableData.sort((a, b) => {
            const nameA = a.name.toLowerCase();
            const nameB = b.name.toLowerCase();
            return this.sortByOrder(nameA, nameB, sortOrderValue);
          });
          break;
        case 1:
          // sort by price
          res = tableData.sort((a, b) => this.sortByOrder(a.price, b.price, sortOrderValue));
          break;
        case 2:
          // sort by volume
          res = tableData.sort((a, b) => this.sortByOrder(a.volume24h, b.volume24h, sortOrderValue));
          break;
        case 3:
          // sort by marketcup
          res = tableData.sort(bnSort('marketcap', !sortOrderValue));
          break;
        case 4:
          // sort by price change
          res = tableData.sort((a, b) => this.sortByOrder(a.priceDiff24InPercent, b.priceDiff24InPercent, sortOrderValue));
          break;
        default:
          res = tableData;
          break;
      }

      return res.slice(this.activePage * this.pageSize, (this.activePage * this.pageSize) + this.pageSize);
    },
  },

  methods: {
    getFilterIndex(value) {
      this.filterState = !this.filterState;
      this.sortOrder[value] = !this.sortOrder[value];
      const sortOrder = this.sortOrder[value];

      this.filterParametr.value = value;
      this.filterParametr.sortOrder = sortOrder;
      this.$emit('sortingChange');
    },

    sortAscending(a, b) {
      if (typeof a === 'string') {
        if (a < b) return -1;
        if (a > b) return 1;
      } else {
        if (a.lt(b)) return -1;
        if (a.gt(b)) return 1;
      }
      return 0;
    },

    sortDescending(a, b) {
      if (typeof a === 'string') {
        if (a > b) return -1;
        if (a < b) return 1;
      } else {
        if (a.gt(b)) return -1;
        if (a.lt(b)) return 1;
      }
      return 0;
    },

    sortByOrder(value1, value2, sortOrder) {
      if (sortOrder) {
        return this.sortAscending(value1, value2);
      }
      return this.sortDescending(value1, value2);
    },
  },

  components: {
    ECatalogueTableItem,
    EIcon,
    EContainerScroll,
    TransitionList,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-catalogue-table {
  $this: &;

  height: 100%;
  display: flex;
  flex-flow: column;

  &__header {
    display: flex;
    flex-shrink: 0;
    margin-bottom: 15px;
    padding-left: 6px;
    padding-right: 26px;
  }


  &__header-column {
    position: relative;
    box-sizing: border-box;
    padding-left: 20px;
    color: #7f7f7f;
    text-align: right;
    flex-basis: 19.5%;
    max-width: 19.5%;
    min-width: 19.5%;
    cursor: pointer;

    @include r(1300) {
      flex-basis: 17.5%;
      max-width: 17.5%;
      min-width: 17.5%;
    }
  }

  &__header-name {
    text-align: left;
    padding-left: 13px;
  }

  &__header-graph {
    cursor: default;
    padding-right: 22px;
    flex-basis: 22%;
    max-width: 22%;

    &:hover {
      color: #7f7f7f;
    }

    @include r(1300) {
      flex-basis: 31%;
      max-width: 31%;
    }
  }

  &__item {
    position: relative;

    &:hover {
      z-index: 2;
    }
  }

  &__list {
    flex: 1;
    height: 100%;
    margin: 10px 0 8px 0;
    padding: 0 5px 0 0;
  }

  &__filter {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin-right: 3px;
    top: -1px;
  }

  &__filter-arrow {
    width: 7px;
    height: 5px;

    svg {
      fill: #DBE1E5;
      transition: $transition-time;

      &:hover {
        fill: #8384AB;
      }
    }
  }

  &__filter-arrow_down {
    margin-top: 2px;
  }

  &__header-column {
    font-size: 12px;
    font-weight: 600;
    color: rgba(0, 0, 0, 0.5);
    transition: fill $transition-time, color $transition-time;
    white-space: nowrap;

    &:hover {
      color: $color-blue;
    }
  }

  &__header-column_high {
    color: $color-blue;

    #{$this}__filter-arrow_down {
      svg {
        fill: $color-blue;
      }
    }
  }

  &__header-column_low {
    color: $color-blue;

    #{$this}__filter-arrow_up {
      svg {
        fill: $color-blue;
      }
    }
  }

  &__content {
    height: 100%;
    box-sizing: border-box;
    padding: 0 5px;
    width: calc(100% - 10px);
    padding-right: 16px;
  }

  &__dropdown {
    display: inline-block;
    position: relative;
  }

  &__dropdown-current {
    color: $color-blue;
    display: flex;
    font-size: 12px;
    font-weight: 600;
    padding-right: 8px;
    position: relative;

    .e-icon {
      height: 4px;
      position: absolute;
      top: 50%;
      right: 0;
      margin-top: -2px;
      width: 6px;

      svg {
        fill: $color-blue;
      }
    }
  }
}
</style>



