/**
 * @module store/wallets/mutations
 * @version 1.0.0
 */
import * as types from '@/store/mutation-types';
import { PRIVATE_KEY_TYPE } from '@/util/constants';
import { isActiveRemoved } from './helpers';

const mutations = {
  [types.CREATE_WALLET](state, wallet) {
    // just in case we generated the same wallet as exists
    const address = wallet.address;
    if (!state.localWallets.find(w => w.address === address)) {
      state.localWallets.push(wallet);
    }
    state.newWalletAddress = address;
  },

  [types.UPSERT_WALLET](state, wallet) {
    if (wallet.type === PRIVATE_KEY_TYPE) {
      if (!state.localWallets.find(w => w.address === wallet.address)) {
        state.localWallets.push(wallet);
      }
    } else {
      state.providedWallets.push(wallet);
    }
  },

  [types.SET_LOCAL_WALLETS](state, wallets) {
    // optimisation: local wallets are set before active wallet is selected, so not null-ing activeWallet
    state.localWallets = wallets;
  },

  // /**
  //  * Mutation: Удаляет все кошельки в локалсторедже
  //  */
  // [types.CLEAR_LOCAL_WALLETS](state) {
  //   if (state.activeWallet && state.activeWallet.type === PRIVATE_KEY_TYPE) {
  //     state.activeWallet = null;
  //   }
  //   state.localWallets = [];
  // },

  // /**
  //  * Mutation: Удаляет выбранный кошелек в локалсторедже
  //  */
  // [types.CLEAR_ACTIVE_WALLET](state) {
  //   if (state.activeWallet && state.activeWallet.type === PRIVATE_KEY_TYPE) {
  //     const index = state.localWallets.findIndex(wallet => wallet.address === state.activeWallet.address);
  //     if (index !== -1) {
  //       state.localWallets.splice(index, 1);
  //     }
  //     state.activeWallet = null;
  //   }
  // },

  /**
   * Mutation: Удаляет выбранный кошелек в локалсторедже
   */
  [types.FORGET_WALLET](state, address) {
    const index = state.localWallets.findIndex(w => w.address === address);
    if (index !== -1) {
      state.localWallets.splice(index, 1);
    } else {
      throw new Error('FORGET_WALLET wallet not found in state.localWallets');
    }
  },

  [types.SET_PROVIDED_WALLETS](state, wallets) {
    if (isActiveRemoved(state.activeWallet, state.providedWallets, wallets)) {
      state.activeWallet = null;
    }
    state.providedWallets = wallets;
  },

  [types.SET_LEDGER_WALLETS](state, wallets) {
    state.ledgerWallets = wallets;
  },

  [types.SET_ACTIVE_WALLET](state, wallet) {
    state.activeWallet = wallet;
  },

  [types.SET_ACTIVE_WALLET_NOT_NEW](state) {
    const newAddress = state.newWalletAddress;
    if (newAddress && state.activeWallet && state.activeWallet.address === newAddress) {
      state.newWalletAddress = null;
    }
  },

  [types.SET_TOTAL_WALLETS](state, totalWallets) {
    state.totalWallets = totalWallets;
    state.totalWalletsLoaded = true;
  },
};

export default mutations;



