<template>
  <div class="e-panel-error">
    <div class="e-panel-error__body">
      <div class="e-panel-error__icon-wrapp">
        <div class="e-panel-error__icon">
          <e-icon :src="icon"/>
        </div>
      </div>

      <div class="e-panel-error__title">
        <slot name="title"></slot>
      </div>

      <div class="e-panel-error__descr">
        <slot name="descr"></slot>
      </div>

      <slot name="after"/>
    </div>
  </div>
</template>

<script>
import iconTranding from '@/icons/e-icon_trading.svg';

import EIcon from '@/components/EIcon';

export default {
  name: 'e-panel-error',

  props: {
    buttons: {
      type: Boolean,
      default: false,
    },
    icon: {
      type: String,
      default: iconTranding,
    },
  },

  data() {
    return {
      iconTranding,
    };
  },

  components: {
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-panel-error {
  $this: &;

  background-color: #fff;
  height: 100%;
  padding: 43px;
  box-sizing: border-box;
  box-shadow: 0 1px 3px rgba(black, .08);
  position: relative;

  &__icon-wrapp {
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__icon {
    width: 36px;
    height: 20px;
    margin-right: 1px;
  }

  &__body {
    display: flex;
    align-items: center;
    flex-direction: column;
    position: absolute;
    padding: 0 43px;
    box-sizing: border-box;
    width: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &__title {
    font-size: 18px;
    color: $color-blue;
    font-weight: 400;
    display: inline-block;
    position: relative;
    padding-bottom: 13px;
    margin-bottom: 15px;

    &:before {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: 2px;
      background-color: $color-blue;
      border-radius: 100px;
    }
  }

  &__descr {
    text-align: center;
    font-size: 14px;
    font-weight: 400;
    color: $color-ultramarine;
    line-height: 1.5;
  }
}
</style>



