import axios from 'axios';
// import { INTERVAL_HEARTBEAT_FRONTEND_SERVER } from '@/config';
import { withRetryForAsync } from '@/util/withRetry';

const http = axios.create({
  headers: {
    'Cache-Control': 'no-cache',
  },
});

const testAttempt = () => http.get('/static/test-connect.json');

// eslint-disable-next-line import/prefer-default-export
export const testConnection = async () => {
  try {
    const { data } = await withRetryForAsync(testAttempt);
    return data.connect === true;
  } catch (e) {
    return false;
  }
};

