<template>
  <div class="the-token-catalogue">
    <transition-content>
      <div class="the-token-catalogue__wrapp">
        <e-bottom-layout
          bodyClass="the-token-catalogue__body"
        >
          <template slot="body">
            <div class="the-token-catalogue__tabs">
              <e-button-tabs
                :tabs="ALL_TABS"
                :active-tab.sync="activeTab"
              />
              <div class="the-token-catalogue__search">
                <input
                  type="search"
                  class="the-token-catalogue__search-input"
                  v-model="searchString"
                  placeholder="Search Tokens…"
                >
                <e-icon :src="iconSearch"/>
              </div>
            </div>

            <template v-if="loaded">
              <the-catalogue-table
                v-if="tokens.length"
                key="table"
                class="the-token-catalogue__table"
                :class="{ 'the-token-catalogue__table_no-pages': pageCount < 2 }"
                :activePage="activePage"
                :pageSize="PAGE_SIZE"
                :tableParams="tokens"
                @sortingChange="activePage = 0"
              />

              <div
                v-else
                key="empty"
                class="the-token-catalogue__empty-search"
              >
                <e-circle-icon-content
                  :icon="iconError"
                  title="No results found"
                  text="Try adjusting your search or if the token is not in this list - list it manually."
                  background="red"
                  :content="true"
                  iconSize="lg"
                />
                <router-link :to="$popupRoute(POPUP_LIST_TOKEN)">
                  <e-button
                    fill="green"
                    padding="spacing"
                    iconMargin="primary"
                    :icon="iconCompose"
                  >
                    list a token
                  </e-button>
                </router-link>
              </div>
            </template>
          </template>

          <e-pagination
            v-if="loaded && pageCount > 1"
            slot="footer"
            v-model="activePage"
            :page-count="pageCount"
            class="the-token-catalogue__paging"
          />
        </e-bottom-layout>
      </div>
    </transition-content>
  </div>
</template>

<script>
/**
 * Компонент каталога тоенов
 *
 * @version 1.0.1
 */

import { mapState } from 'vuex';

// Components
import EBottomLayout from '@/components/EBottomLayout';
import EButton from '@/components/EButton';
import EButtonTabs from '@/components/EButtonTabs';
import ECircleIconContent from '@/components/ECircleIconContent';
import EIcon from '@/components/EIcon';
import EPagination from '@/components/EPagination';
import TheCatalogueTable from '@/containers/blocks/TheCatalogueTable';

// Transitions
import TransitionContent from '@/containers/transitions/TransitionContent';

// Icons
import iconCompose from '@/icons/e-icon_compose-with-pen.svg';
import iconError from '@/icons/e-icon_error.svg';
import iconSearch from '@/icons/e-icon_search_wide.svg';

import { routesMixin, searchTokenMixin } from '@/util/mixins';

const TAB_ALL = 'All tokens';
const TAB_TRENDING = 'Trending';
const TAB_MY_TOKENS = 'Favorites';

const ALL_TABS = [TAB_ALL, TAB_TRENDING, TAB_MY_TOKENS];

// Количество отображаемых на одной старнице токенов
const PAGE_SIZE = 20;

// TODO preloader
// Нумерация страниц с 0, но пользователю отображаем с 1
export default {
  name: 'the-token-catalogue',

  data() {
    return {
      activeTab: null,
      searchString: '',
      activePage: 0,
    };
  },

  computed: {
    ...mapState({
      loaded: state => state.tokens.tokensLoaded,

      tokens(state, getters) {
        if (!this.searchString && this.activeTab === TAB_MY_TOKENS) {
          return getters['tokens/myTokens'];
        } else if (!this.searchString && this.activeTab === TAB_TRENDING) {
          return getters['tokens/sortedTokens'].filter(item => item.volume24h.gt(0));
        }
        return this.$searchTokens(this.searchString);
      },
    }),

    // Общее количество страниц для отображения
    pageCount() {
      return Math.ceil(this.tokens.length / PAGE_SIZE);
    },
  },

  watch: {
    activeTab() {
      this.activePage = 0;
    },
  },

  created() {
    Object.assign(this, {
      iconSearch,
      iconError,
      iconCompose,
      PAGE_SIZE,
      ALL_TABS,
    });
    this.$store.dispatch('tokens/listenToTokensChanges');
    this.$store.dispatch('tokens/loadTokens');
  },

  beforeDestroy() {
    this.$store.dispatch('tokens/stopListeningToTokensChanges');
  },

  components: {
    EButton,
    EButtonTabs,
    ECircleIconContent,
    TheCatalogueTable,
    TransitionContent,
    EIcon,
    EBottomLayout,
    EPagination,
  },

  mixins: [routesMixin, searchTokenMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-token-catalogue {
  height: calc(100% + 46px);
  margin-top: -46px;

  &__wrapp {
    height: 100%;
  }

  &__body {
    display: flex;
    flex-direction: column;
  }

  &__tabs {
    margin-bottom: 22px;
    display: flex;
    justify-content: space-between;
    padding: 0 5px;
    min-height: 30px;
  }

  &__table {
    height: calc(100% - 200px);
  }

  &__search {
    position: relative;
    height: 24px;

    .e-icon {
      position: absolute;
      left: 10px;
      top: 50%;
      transform: translateY(-50%);
      height: 12px;
      width: 12px;
      fill: $color-blue;
    }
  }

  &__search-input {
    height: 100%;
    box-shadow: inset 0 0 3px rgba(72, 78, 84, 0.13);
    border-radius: 3px;
    padding-left: 29px;
    font-weight: bold;
    line-height: 14px;
    font-size: 11px;
  }

  &__paging {
    margin: 21px auto 28px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 800;
    font-size: 10px;
    letter-spacing: 0.7px;
    text-transform: uppercase;
    color: #0123E7;
  }

  &__paging-main-btn {
    margin: 0 5px;
    height: 100%;
    width: 149px;
    background: #FFFFFF;
    box-shadow:  2px 3px rgba(0, 0, 0, 0.0770211);
    border-radius: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__page-btn {
    margin: 0 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 40px;
    min-width: 40px;
    background: #FFFFFF;
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.08);
    border-radius: 30px;
    transition: $transition-time;

    &:hover:not(&_active) {
      background-color: #F3F6FF;
      box-shadow: inset 0 1px 5px rgba(148,165,180,0.14);
    }
  }

  &__table_no-pages {
    margin-bottom: (21px * 2) + 40px;
  }

  &__page-btn_active {
    color: #fff;
    background: #0123E7;
    box-shadow: inset 0 1px 5px #09168A;
    cursor: default;
  }

  &__empty-search {
    align-items: center;
    display: flex;
    flex-direction: row;
    padding: 66px 20px;
    justify-content: space-between;

    @include r(1200) {
      padding: 40px 2px;
    }
  }

  &__paging-main-btn {
    box-shadow: 0 2px 3px 0 rgba(0,0,0,0.08);
    transition: $transition-time;

    &.is-disable {
      background-color: #9A9EA7;
      cursor: default;
      color: #FFFFFF;

      .e-icon svg {
        fill: #fff;
      }
    }

    &:hover:not(.is-disable) {
      background-color: #F3F6FF;
      box-shadow: inset 0 1px 5px rgba(148,165,180,0.14);
    }

    .e-icon {
      height: 10px;
      width: 10px;

      svg {
        fill: $color-blue;
      }
    }
  }

  &__paging-main-btn_prev {
    .e-icon {
      margin-right: 9px;
    }
  }

  &__paging-main-btn_next {
    .e-icon {
      margin-left: 9px;
    }
  }
}
</style>



