<template>
  <div class="e-items-title">
    <div class="e-items-title__textual">
      <div
        class="e-items-title__icon"
        :class="iconColor ? 'e-items-title__icon_color_' + iconColor : ''"
      >
        <e-icon :src="icon" />
      </div>
      {{ counter === 0 && counterInTitle
          ? 'no'
          : counterInTitle
            ? counter
            : ''
      }} <span v-html="title"></span>
    </div>
  </div>
</template>

<script>
import EIcon from '@/components/EIcon';

export default {
  name: 'e-items-title',

  props: {
    icon: {
      type: String,
      default: '',
    },
    title: {
      type: String,
      default: '',
    },
    counter: {
      type: Number,
      default: 0,
    },
    iconColor: {
      type: String,
      default: '',
    },
    counterInTitle: {
      type: Boolean,
      default: true,
    },
  },

  components: {
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-items-title {
  color: $color-ultramarine;
  font-size: 14px;
  font-weight: bold;
  text-transform: uppercase;
  position: relative;

  &:before {
    background-color: $color-light-grey;
    content: '';
    height: 1px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
  }

  @include r(1200) {
    font-size: 12px;
  }

  &__textual {
    background-color: $color-white;
    display: inline-block;
    padding-left: 30px;
    padding-right: 9px;
    position: relative;
    z-index: 10;
    letter-spacing: 1.08px;

    @include r(1200) {
      padding-left: 25px;
    }
  }

  &__icon {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    height: 20px;
    width: 20px;

    .e-icon svg {
      fill: $color-blue;
    }

    @include r(1200) {
      height: 15px;
      width: 15px;
    }
  }

  &__icon_color_green {
    .e-icon svg {
      fill: $color-green;
    }
  }
}
</style>



