import { bufferToHex, fromRpcSig, toBuffer, sha3 } from 'ethereumjs-util';
import { PRIVATE_KEY_TYPE, METAMASK_WALLET_TYPE, LEDGER_WALLET_TYPE } from '@/util/constants';

const signWithoutPrefix = ({ web3, address, data }) => web3.eth.sign(data, address);

const signWithPrefix = ({ web3, address, data }) => {
  const prefix = toBuffer('\x19Ethereum Signed Message:\n');
  const buffer = prefix.constructor.concat([prefix, toBuffer('32'), toBuffer(data)]);
  const hashed = bufferToHex(sha3(buffer));
  return web3.eth.sign(hashed, address);
};

const signFunctions = {
  [PRIVATE_KEY_TYPE]: signWithoutPrefix,
  [METAMASK_WALLET_TYPE]: signWithPrefix,
  [LEDGER_WALLET_TYPE]: signWithoutPrefix,
};

export default async ({ web3, wallet: { address, type }, data }) => {
  const signed = await signFunctions[type]({ web3, address, data });
  const { v, r, s } = fromRpcSig(signed);
  return {
    v,
    r: bufferToHex(r),
    s: bufferToHex(s),
  };
};



