import Vue from 'vue';
import Router from 'vue-router';
import Meta from 'vue-meta';
import { isValidEthereumAddress, toChecksumEthereumAddress } from '@/util/crypto';
import store from '@/store';
import { ASYNC_STATES, NOT_FOUND_PAGE } from '@/util/constants';
import { CONNECTION_STATUS } from '@/store/constants';
import getPopupPages from './getPopupPages';
import fetchDefaultToken from './fetchDefaultToken';
import affiliate from './affiliate';

const PageCharts = () => import(/* webpackChunkName: "charts" */ '@/containers/pages/PageCharts');
const Page404 = () => import(/* webpackChunkName: "page404" */ '@/containers/pages/Page404');
const PageIndexNew = () => import(/* webpackChunkName: "PageIndexNew" */ '@/containers/pages/PageIndexNew');
const PageNews = () => import(/* webpackChunkName: "PageNews" */ '@/containers/pages/PageNews');
const PagePostFirst = () => import(/* webpackChunkName: "PagePostFirst" */ '@/containers/pages/PagePostFirst');
const PagePostSecond = () => import(/* webpackChunkName: "PagePostSecond" */ '@/containers/pages/PagePostSecond');
const PagePostThird = () => import(/* webpackChunkName: "PagePostThird" */ '@/containers/pages/PagePostThird');
const PageAboutUs = () => import(/* webpackChunkName: "PageAboutUs" */ '@/containers/pages/PageAboutUs');
const PageAboutEthermium = () => import(/* webpackChunkName: "PageAboutEthermium" */ '@/containers/pages/PageAboutEthermium');
const PageAboutDecentralization = () => import(/* webpackChunkName: "PageAboutDecentralization" */ '@/containers/pages/PageAboutDecentralization');
const PageSupport = () => import(/* webpackChunkName: "PageSupport" */ '@/containers/pages/PageSupport');
const PageFees = () => import(/* webpackChunkName: "PageFees" */ '@/containers/pages/PageFees');
const PageNoMoreListings = () => import(/* webpackChunkName: "PageFees" */ '@/containers/pages/PageNoMoreListings');
const PageNewsTradingView = () => import(/* webpackChunkName: "PageFees" */ '@/containers/pages/PageNewsTradingView');

Vue.use(Router);
Vue.use(Meta);

/**
 * Список роутов для которых может возникать попап
 */
const BACKGROUND_ROUTES_FOR_POPUP = ['index', 'tokens', 'token', 'main', 'news', 'postfirst', 'postsecond', 'postthird', 'aboutus', 'aboutethermium', 'aboutdecenralization', 'support'];

const routes = [
  {
    path: '/',
    name: 'index',
    component: PageIndexNew,
    children: getPopupPages('index'),
  },
  {
    path: '/404',
    name: NOT_FOUND_PAGE,
    component: Page404,
  },
  {
    path: '/main',
    name: 'main',
    component: PageIndexNew,
    children: getPopupPages('main'),
  },
  {
    path: '/news',
    name: 'news',
    component: PageNews,
    children: getPopupPages('news'),
  },
  {
    path: '/stop-limit-and-market-order-types',
    name: 'postfirst',
    component: PagePostFirst,
    children: getPopupPages('postfirst'),
  },
  {
    path: '/ledger-nano-s-wallet-now-supported',
    name: 'postsecond',
    component: PagePostSecond,
    children: getPopupPages('postsecond'),
  },
  {
    path: '/platform-launch',
    name: 'postthird',
    component: PagePostThird,
    children: getPopupPages('postthird'),
  },
  {
    path: '/about-us',
    name: 'aboutus',
    component: PageAboutUs,
    children: getPopupPages('aboutus'),
  },
  {
    path: '/what-is-ethermium',
    name: 'aboutethermium',
    component: PageAboutEthermium,
    children: getPopupPages('aboutethermium'),
  },
  {
    path: '/what-is-decentralization',
    name: 'aboutdecenralization',
    component: PageAboutDecentralization,
    children: getPopupPages('aboutdecenralization'),
  },
  {
    path: '/no-more-listings',
    name: 'nomorelistings',
    component: PageNoMoreListings,
    children: getPopupPages('nomorelistings'),
  },
  {
    path: '/tradingview',
    name: 'tradingview',
    component: PageNewsTradingView,
    children: getPopupPages('tradingview'),
  },
  {
    path: '/contact-us',
    name: 'support',
    component: PageSupport,
    children: getPopupPages('support'),
  },
  {
    path: '/fees',
    name: 'fees',
    component: PageFees,
    children: getPopupPages('fees'),
  },
  {
    path: '/a/:aff/t/:token',
    beforeEnter: (to, from, next) => {
      const { aff, token } = to.params;
      affiliate(aff); // async
      next({ name: 'token', params: { token } });
    },
  },
  {
    path: '/a/:aff',
    beforeEnter: (to, from, next) => {
      affiliate(to.params.aff); // async
      next({ name: 'index' });
    },
  },
  {
    path: '/t',
    name: 'tokens',
    beforeEnter: async (to, from, next) => {
      const token = await fetchDefaultToken();
      if (token) {
        next({
          name: 'token',
          params: { token },
          // replace: true,
        });
      } else {
        // TODO: smarter maintenance page
        next({
          name: NOT_FOUND_PAGE,
          // replace: true,
        });
      }
    },
  },
  {
    path: '/t/:token',
    name: 'token',
    component: PageCharts,
    beforeEnter: async (to, from, next) => {
      const { token: paramToken, ...restParams } = to.params;
      await store.dispatch('tokens/loadTokens');
      if (isValidEthereumAddress(paramToken)) {
        const checksumAddress = toChecksumEthereumAddress(paramToken);
        if (checksumAddress !== paramToken) {
          next({
            name: to.name,
            params: {
              ...restParams,
              token: checksumAddress,
            },
            // replace: true,
          });
        } else if (store.getters['tokens/isTokenContractOnExchange'](paramToken)) {
          const activeToken = store.getters['tokens/tokenByContractSimple'](paramToken);
          next({
            name: to.name,
            params: {
              ...restParams,
              token: `${activeToken.name}-${activeToken.shortName}`,
            },
            // replace: true,
          });
        } else {
          await store.dispatch('tokens/setNewTokenAsActive', paramToken);
          if (store.state.tokens.setActiveTokenState === ASYNC_STATES.ERROR) {
            next({
              name: NOT_FOUND_PAGE,
              // replace: true,
            });
          } else {
            next();
          }
        }
      } else if (paramToken === undefined) {
        next({
          name: 'tokens',
          // replace: true,
        });
      } else {
        const tokenAddress = store.getters['tokens/getTokenContractByNameAndShortName'](paramToken);
        if (tokenAddress) {
          await store.dispatch('tokens/setActiveToken', tokenAddress);
          next();
        } else {
          next({
            name: NOT_FOUND_PAGE,
            // replace: true,
          });
        }
      }
    },
    children: getPopupPages('token'),
  },
  {
    path: '*',
    beforeEnter(to, from, next) {
      next({
        name: NOT_FOUND_PAGE,
        // replace: true,
      });
    },
  },
];

const router = new Router({
  mode: 'history',
  routes,
});

// Prevent user navigation if disconnected
router.beforeEach((to, from, next) => {
  if (store.state.backendConnectionStatus === CONNECTION_STATUS.OK) {
    next();
  } else if (from.name !== null) {
    next(false);
  } else {
    next();
  }
});

router.beforeEach((to, from, next) => {
  if (store.getters['ui/isPopupShowing'] && BACKGROUND_ROUTES_FOR_POPUP.includes(to.name)) {
    store.commit('ui/HIDE_POPUP');
  }
  next();
});

export default router;



