<template>
  <div class="the-signup-options">
    <transition-list>
      <div class="the-signup-options__option" v-for="item in signUpOptions" :key="item.title">
        <div class="the-signup-options__side">
          <div
            class="the-signup-options__option-circle"
            :class="item.iconFill ? 'the-signup-options__option-circle_fill-' + item.iconFill : ''"
          >
            <div
              class="the-signup-options__option-icon"
              :class="[item.iconWidth ? 'the-signup-options__option-icon_' + item.iconWidth : '']"
            >
              <e-icon :src="item.icon" />
            </div>
          </div>
          <div class="the-signup-options__textual">
            <div class="the-signup-options__option-title">{{ item.title }}</div>
            <div class="the-signup-options__option-text">{{ item.text }}</div>
          </div>
        </div>
        <div class="the-signup-options__option-btns">
          <div class="the-signup-options__option-btn">
            <e-button
              fill="green"
              size="s"
              width="full"
              @click="item.btns[0].handler"
              v-if="hideBtn !== 1"
            >
              {{ item.btns[0].label }}
            </e-button>
          </div>
          <div class="the-signup-options__option-btn">
            <e-button
              fill="white"
              size="s"
              width="full"
              @click="item.btns[1].handler"
              v-if="hideBtn !== 2"
            >
              {{ item.btns[1].label }}
            </e-button>
          </div>
        </div>
      </div>
    </transition-list>
  </div>
</template>

<script>
import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';

import TransitionList from '@/containers/transitions/TransitionList';

export default {
  name: 'the-signup-options',

  props: {

    signUpOptions: {
      type: Array,
      default() {
        return [];
      },
    },

    hideBtn: {
      type: Number,
      default: 0,
    },

  },

  components: {
    EButton,
    EIcon,
    TransitionList,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-signup-options {
  margin-top: 40px;

  @include h(800) {
    margin-top: 20px;
  }

  &__side {
    display: flex;
    align-items: center;
  }

  &__textual {
    text-align: left;
  }

  &__option {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 48px;

    &:last-of-type {
      margin-bottom: 0;
    }

    @include r(1200) {
      margin-bottom: 43px;
    }

    @include h(980) {
      margin-bottom: 20px;
    }
  }

  &__option-circle {
    position: relative;
    top: 9px;
    width: 60px;
    height: 60px;
    background-color: #FFFFFF;
    box-shadow: 0 12px 24px 0 rgba(148,165,180,0.14);
    border-radius: 50%;
    margin-right: 30px;

    @include r(1200) {
      height: 50px;
      margin-right: 21px;
      width: 50px;
    }
  }

  &__option-circle_fill-green {
    fill: #17DA9D;
  }

  &__option-circle_fill-blue {
    fill: $color-blue;
  }

  &__option-icon {
    height: 26px;
    width: 26px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    @include r(1200) {
      height: 20px;
      width: 20px;
    }
  }

  &__option-icon_wide {
    width: 22px;
    height: 22px;

    @include r(1200) {
      height: 16px;
      width: 16px;
    }
  }

  &__option-title {
    margin-top: 19px;
    color: #0d105b;
    font-size: 20px;
    font-weight: 600;
    line-height: 27px;

    @include r(1200) {
      font-size: 16px;
      margin-top: 16px;
      line-height: 23px;
    }
  }

  &__option-text {
    margin-top: 8px;
    color: #0d105b;
    font-size: 16px;
    font-weight: 400;
    line-height: 21px;

    @include r(1200) {
      margin-top: 0;
    }
  }

  &__option-btns {
    display: flex;
  }

  &__option-btn {
    min-width: 150px;

    &:not(:last-child) {
      margin-right: 20px;
    }

    @include r(1200) {
      min-width: 120px;
    }
  }
}
</style>



