<template>
  <transition
    name="transition-dropdown"
    :duration="{ enter, leave }"
    appear
    v-on="$listeners"
  >
    <slot></slot>
  </transition>
</template>

<script>
export default {
  name: 'transition-dropdown',

  props: {
    enter: {
      type: Number,
      default: 300,
    },

    leave: {
      type: Number,
      default: 300,
    },
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.transition-dropdown-enter-active, .transition-dropdown-leave-active {
  transition: .3s;
  transition-property: transform, opacity;
  will-change: transform, opacity;

  &.transition-dropdown-absolute {
    position: absolute;
    width: 100%;
    box-sizing: border-box;
  }
}

.transition-dropdown-enter, .transition-dropdown-leave-to {
  opacity: 0;
  transform: translateY(-50px);

  &.transition-dropdown-up {
    transform: translateY(50px);
  }
}
</style>



