<template>
  <div
    class="e-container-scroll"
    :style="proportionStyle"
    :ref="'eContainerScroll'"
  >
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'e-container-scroll',

  data() {
    return {
      settings: {
        maxScrollbarLength: this.maxScrollbarLength,
        minScrollbarLength: this.minScrollbarLength,
        wheelSpeed: 1,
        wheelPropagation: true,
        suppressScrollX: true,
      },
    };
  },

  props: {
    maxScrollbarLength: {
      type: Number,
    },

    minScrollbarLength: {
      type: Number,
      default: 90,
    },

    swicher: {
      type: Boolean,
      default: true,
    },

    bottom: {
      type: Boolean,
      default: false,
    },
  },

  mounted() {
    if (this.bottom) {
      this.scrollToBottom();
    }
  },

  computed: {
    proportionStyle() {
      return {
        'max-height': `${this.maxScrollbarLength}px`,
        'min-height': `${this.minScrollbarLength}px`,
      };
    },
  },

  methods: {
    scrollToTop() {
      this.$nextTick(() => {
        if (this.$refs.eContainerScroll) {
          this.$refs.eContainerScroll.scrollTop = 0;
        }
      });
    },
    scrollToBottom() {
      this.$nextTick(() => {
        if (this.$refs.eContainerScroll) {
          this.$refs.eContainerScroll.scrollTop = this.$refs.eContainerScroll.scrollHeight;
        }
      });
    },
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-container-scroll {
  position: relative;
  overflow-y: auto;
  height: 100%;

  &::-webkit-scrollbar {
    width: 2px;
    height: 2px;
  }

  &::-webkit-scrollbar-track {
    background: $color-violet;
    border-radius: 2px;
  }

  &::-webkit-scrollbar-thumb {
    background: $color-blue;
    border-radius: 2px;
  }
}
</style>



