import { isEdge, isIE, isSafari } from '@/util/browser/browserDetection';
import TransportU2F from '@ledgerhq/hw-transport-u2f';

let cache;

/**
 * Can't be rejected (i.e. always resolves).
 * @returns {Promise<*>}
 */
export default async () => {
  if (cache === undefined) {
    if (isIE || isEdge || isSafari) return false;
    try {
      return await TransportU2F.isSupported();
    } catch (e) {
      return false;
    }
  }
  return cache;
};



