<template>
  <div class="e-circle-icon-content">
    <div class="e-circle-icon-content__icon">
      <e-circle-icon v-if="!iconUpdate" :icon="icon" size="large" :background="background" :iconSize="iconSize" />
      <the-update-button v-if="iconUpdate" />
    </div>
    <div class="e-circle-icon-content__textual">
      <div class="e-circle-icon-content__title" v-if="title">
        {{ title }}
      </div>
      <div class="e-circle-icon-content__txt">
        {{ text }}
      </div>
      <div class="e-circle-icon-content__content" v-if="content">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
import ECircleIcon from '@/components/ECircleIcon';
import TheUpdateButton from '@/containers/blocks/TheUpdateButton';

export default {
  name: 'e-circle-icon-content',

  props: {
    icon: {
      type: String,
      default: '',
    },
    title: {
      type: String,
      default: null,
    },
    text: {
      type: String,
      default: null,
    },
    content: {
      type: Boolean,
      default: false,
    },
    background: {
      type: String,
      default: 'default',
    },
    iconSize: {
      type: String,
      default: null,
    },
    iconUpdate: {
      type: Boolean,
      default: false,
    },
  },

  components: {
    ECircleIcon,
    TheUpdateButton,
  },

};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-circle-icon-content {
  display: flex;

  &__content {
    display: flex;
    flex-direction: column;
  }

  &__title {
    color: $color-green;
    font-size: 20px;
    line-height: 1.35;

    @include r(1200) {
      font-size: 16px;
    }
  }

  &__textual {
    margin-top: 28px;
    margin-left: 33px;
    max-width: 384px;
    line-height: 1.3;

    @include r(1200) {
      margin-top: 17px;
      margin-left: 30px;
    }
  }

  &__txt {
    color: $color-dark-blue;
    font-size: 20px;
    font-weight: 400;

    @include r(1200) {
      font-size: 18px;
    }
  }

  &__content {
    align-items: flex-start;
    margin-top: 22px;

    @include r(1200) {
      margin-top: 17px;
    }
  }
}
</style>




