<template>
  <transition
    name="transition-content"
    :duration="{ enter, leave }"
    appear
    v-on="$listeners"
  >
    <slot></slot>
  </transition>
</template>

<script>
export default {
  name: 'transition-content',

  props: {
    enter: {
      type: Number,
      default: 700,
    },

    leave: {
      type: Number,
      default: 700,
    },
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.transition-content-enter-active, .transition-content-leave-active {
  position: absolute;
  width: 100%;
  box-sizing: border-box;
  transition: 0.5s;
  transition-property: transform, opacity;
  will-change: transform, opacity;
}

.transition-content-enter-active {
  transition-delay: 0.2s;
}

.transition-content-enter, .transition-content-leave-to {
  opacity: 0;
  transform: translateX(20%);
}

.transition-content-enter-to {
  transform: translateX(0);
}
</style>



