<template>
  <div class="e-instruction">
    <div class="e-instruction__container">
      <div class="e-instruction__textual"
        v-for="(item, index) in instructionData.textual"
        :key="index"
        v-html="item"
      />
    </div>
    <div class="e-instruction__steps"
      v-for="(item, index) in instructionData.steps"
      :key="index"
    >
      <div class="e-instruction__step-title"><span class="e-instruction__step-index">{{index + 1}}.</span>{{item.stepTitle}}</div>
      <ul class="e-instruction__list">
        <li class="e-instruction__step-item"
          v-for="(step, index) in item.stepItems"
          :key="index"
          v-html="step"
        />
      </ul>
    </div>
    <div
      v-if="instructionData.importButton"
      class="e-instruction__buttons"
    >
      <e-button
        fill="green"
        size="s"
        @click="showWalletImport"
      >
        import wallet
      </e-button>
    </div>
  </div>
</template>

<script>
import EButton from '@/components/EButton';
import { POPUP_IMPORT } from '@/store/constants';
import { routesMixin } from '@/util/mixins';

export default {
  name: 'e-instruction',

  props: {
    instructionData: {
      type: Object,
      required: true,
    },
  },

  methods: {
    showWalletImport() {
      this.$router.push(this.$popupRoute(POPUP_IMPORT));
    },
  },

  components: {
    EButton,
  },
  mixins: [routesMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-instruction {
  color: $color-dark-blue;
  font-size: 16px;
  font-weight: 400;

  &__step-title {
    font-weight: 600;
  }

  &__step-index {
    display: inline-block;
    margin-right: 12px;
  }

  &__step-item {
    padding-left: 24px;
    position: relative;

    &:not(:last-child) {
      margin-bottom: 12px;
    }

    &:before {
      border: 1px solid $color-dark-blue;
      border-radius: 50%;
      content: '';
      height: 6px;
      position: absolute;
      left: 0;
      top: 8px;
      width: 6px;
    }
  }

  &__textual {
    margin-bottom: 22px;
  }

  &__link {
    color: $color-blue;
    transition: color ease $transition-time;
    text-decoration: none;

    &:hover {
      color: darken($color-blue, 20%);
    }
  }

  &__list {
    list-style-type: none;
  }

  &__buttons {
    display: flex;
    flex-direction: row;
    margin-top: 33px;
    justify-content: flex-end;
  }
}
</style>



