<template>
  <div
    class="e-pagination"
    :class="`e-pagination_${type}`"
  >
    <button
      type="button"
      class="e-pagination__nav-btn e-pagination__nav-btn_prev"
      :disabled="value === 0"
      @click="onPageClick(Math.max(0, value - 1))"
    >
      <e-icon :src="iconArrowLeft"/>
      Previous
    </button>

    <!-- BEGIN: Номера страниц -->
    <button
      v-if="skipPages && pageCount > 0"
      :key="0"
      :class="{ 'e-pagination__page-btn_active': value === 0 }"
      class="e-pagination__page-btn"
      @click="onPageClick(0)"
    >1</button>

    <span
      v-if="skipStart"
      class="e-pagination__skip"
    >...</span>

    <button
      v-for="i of displayedPages"
      :key="i"
      :class="{ 'e-pagination__page-btn_active': i === value }"
      class="e-pagination__page-btn"
      @click="onPageClick(i)"
    >{{ i + 1 }}</button>

    <span
      v-if="skipEnd"
      class="e-pagination__skip"
    >...</span>

    <button
      v-if="skipPages && pageCount > 2"
      :key="pageCount - 1"
      :class="{ 'e-pagination__page-btn_active': pageCount - 1 === value }"
      class="e-pagination__page-btn"
      @click="onPageClick(pageCount - 1)"
    >{{ pageCount }}</button>
     <!-- END: Номера страниц -->

    <button
      type="button"
      class="e-pagination__nav-btn e-pagination__nav-btn_next"
      :disabled="value === pageCount - 1"
      @click="onPageClick(Math.min(value + 1, pageCount))"
    >
      Next
      <e-icon :src="iconArrowRight"/>
    </button>
  </div>
</template>

<script>
/**
 * Компонент кнопок навигации по страницам (пагинации)
 *
 * @version 1.0.2
 */

import EIcon from '@/components/EIcon/EIcon';
import iconArrowLeft from '@/icons/e-icon_arrow_left.svg';
import iconArrowRight from '@/icons/e-icon_arrow_right.svg';

export default {
  name: 'e-pagination',

  props: {
    /**
    * Active page
    *
    * @since Version 1.0.1
    */
    value: {
      type: Number,
      default: 0,
    },

    /**
    * Total namber of pages
    *
    * @since Version 1.0.1
    */
    pageCount: {
      type: Number,
      required: true,
    },

    /**
    * Количество страниц справа и слева от текущий для отборажения (для одной из сторон)
    *
    * @since Version 1.0.2
    */
    neighbors: {
      type: Number,
      default: 1,
    },


    /**
    * The size of the buttons
    * `default, small`
    *
    * @since Version 1.0.1
    */
    type: {
      type: String,
      default: 'default',
      validator: type => ['default', 'small'].includes(type),
    },
  },

  computed: {
    skipPages() {
      return this.pageCount > 2 + this.neighbors;
    },

    skipStart() {
      return this.skipPages && this.value > this.neighbors;
    },

    skipEnd() {
      return this.skipPages && this.value < this.pageCount - 2 - this.neighbors;
    },

    displayedPages() {
      let pages = [];

      if (!this.skipPages) {
        pages = Array.from(Array(this.pageCount).keys());
      } else {
        for (let i = Math.max(this.value - this.neighbors, 1); i <= Math.min(this.value + this.neighbors, this.pageCount - 2); i += 1) {
          pages.push(i);
        }
      }

      return pages;
    },
  },

  created() {
    Object.assign(this, {
      iconArrowLeft,
      iconArrowRight,
    });
  },

  methods: {
    onPageClick(value) {
      this.$emit('input', value);
    },
  },

  components: {
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-pagination {
  $this: &;

  display: flex;
  justify-content: center;
  align-items: center;
  color: $color-blue;
  font-weight: 700;
  line-height: 0;

  &__nav-btn {
    flex: none;
    display: flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;
    height: 100%;
    background-color: #fff;
    transition: $transition-time;

    .e-icon {
      height: 8px;
      width: 8px;

      svg {
        fill: $color-blue;
      }
    }

    &:hover:not(:disabled) {
      background-color: #F3F6FF;
      box-shadow: inset 0 1px 5px rgba(148,165,180,0.14);
    }

    &:disabled {
      background-color: $color-grey;
      cursor: default;
      color: #FFFFFF;

      .e-icon svg {
        fill: #ffffff;
      }
    }
  }

  &__page-btn {
    flex: none;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    background-color: #ffffff;
    transition: $transition-time;

    &:hover:not(&_active) {
      background-color: #F3F6FF;
      box-shadow: inset 0 1px 5px rgba(148,165,180,0.14);
    }
  }

  &__page-btn_active {
    background-color: $color-blue;
    color: $color-white;
    cursor: default;
  }

  &__skip {
    flex: none;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  /* type-specific styles */

  &_default {
    height: 40px;
    font-size: 10px;
    letter-spacing: 0.71px;

    #{$this}__nav-btn {
      width: 149px;
      border-radius: 30px;
      box-shadow: 0 2px 3px rgba(0, 0, 0, 0.08);
    }

    #{$this}__nav-btn_prev {
      margin-right: 5px;
      .e-icon {
        margin-right: 9px;
      }
    }

    #{$this}__nav-btn_next {
      margin-left: 5px;
      .e-icon {
        margin-left: 9px;
      }
    }

    #{$this}__page-btn {
      margin: 0 5px;
      width: 40px;
      border-radius: 30px;
      box-shadow: 0 2px 3px rgba(0, 0, 0, 0.08);
    }

    #{$this}__page-btn_active {
      box-shadow: inset 0 1px 5px #09168a;
    }

    #{$this}__skip {
      margin: 0 5px;
    }
  }

  &_small {
    height: 20px;
    font-size: 9px;
    letter-spacing: 0.64px;

    #{$this}__nav-btn {
      width: 92px;
      border-radius: 10px;
      box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
    }

    #{$this}__nav-btn_prev {
      margin-right: 3px;

      .e-icon {
        margin-right: 6px;
      }
    }

    #{$this}__nav-btn_next {
      margin-left: 3px;

      .e-icon {
        margin-left: 6px;
      }
    }

    #{$this}__page-btn {
      margin: 0 3px;
      width: 20px;
      border-radius: 10px;
      box-shadow: 0 1px 2px rgba(0, 0, 0, 0.08);
    }

    #{$this}__page-btn_active {
      box-shadow: inset 0 1px 5px #09168a;
    }

    #{$this}__skip {
      margin: 0 3px;
    }
  }
}
</style>



