/**
 * @module store/prices/getters
 *
 * @version 1.0.5
 */

import flow from 'lodash/flow';
import { makeObjectForChart, mapCandlesForChart, invertPriceArray } from './helpers';


/**
 * Геттер возвращающий полный массив цен на токен, построенные по трейдам.
 *
 * @since Version 1.0.1
 */
const getPriceArray = state => state.prices;


/**
 * Getter: Если данные для отрисовки графика
 *
 * @since Version 1.0.5
 */
const isDataForChart = state => state.candles && state.candles.length > 0;


/**
 * Геттер возвращающий возможные периоды отображения графика в виде точек.
 */
const availablePeriodsForPointsChart = (state, getters, rootState, rootGetters) => {
  let periodsArray = [
    {
      title: '1d',
      value: 24 * 60 * 60 * 1000,
      resolution: 24 * 6,
    },
    {
      title: '1m',
      value: 30 * 24 * 60 * 60 * 1000,
      resolution: 30 * 4,
    },
    {
      title: '3m',
      value: 3 * 30 * 24 * 60 * 60 * 1000,
      resolution: 3 * 30 * 2,
    },
    {
      title: '1y',
      value: 366 * 24 * 60 * 60 * 1000,
      resolution: 183,
    },
  ];

  const timeFromLastLogin = rootGetters['ui/getLastLogin'] ? new Date().getTime() - rootGetters['ui/getLastLogin'].getTime() : 0;

  if (timeFromLastLogin > 24 * 60 * 60 * 1000) {
    periodsArray = [
      {
        title: 'last visit',
        value: timeFromLastLogin,
        resolution: 120,
      },
      ...periodsArray,
    ];
  }

  periodsArray.push({
    title: 'max',
    value: null,
    resolution: 120,
  });

  return periodsArray;
};


/**
 * Геттер возвращающий данные для отображения графика изменения цен
 * в виде точек.
 *
 *  @param {Date} cutoffTime - Время начиная с которого и до сейчас необходимо построить график.
 *  @param {Number} resolution - на сколько отрезков нужно делить заданный период
 *
 * @since Version 1.0.2
 */
const getDataForPointsChart = (state) => {
  const res = flow([
    mapCandlesForChart,
    invertPriceArray,
    makeObjectForChart,
  ])(state.candles);
  return res;
};

/**
 * Getter: Возвращает флаг загружаются сейчас данные для графика или нет
 *
 * @returns {boolean} - true - сейчас грузятся данные, false - данные не грузятся
 *
 * @since Version 1.0.3
 */
const pricesLoading = state => state.candlesLoading;


/**
 * Getter: Возвращает дату, до которой запрашиваются данные для графика
 *
 * @returns {boolean} - true - сейчас грузятся данные, false - данные не грузятся
 *
 * @since Version 1.0.4
 */
const activePeriod = state => state.activePeriod;


/**
 * Getter: Формирует объек параметров для запроса свечек с бекенда
 *
 * @returns {object}
 *
 * @since Version 1.0.4
 */
const candlesRequestOptions = (state, getters) => (now) => {
  let res;

  if (state.activePeriod === null || state.activePeriod >= getters.availablePeriodsForPointsChart.length - 1) {
    const today = new Date(now.getFullYear(), now.getMonth(), now.getDate());
    res = {
      interval: 2 * 24 * 60 * 60,
      fromDate: new Date(now.getTime() - today.getTime()).toJSON(),
      toDate: now.toJSON(),
    };
  } else {
    const currentInterval = getters.availablePeriodsForPointsChart[state.activePeriod];
    res = {
      interval: Math.round(currentInterval.value / (currentInterval.resolution * 1000)),
      fromDate: new Date(now.getTime() - currentInterval.value).toJSON(),
      toDate: now.toJSON(),
    };
  }

  return res;
};

export default {
  activePeriod,
  availablePeriodsForPointsChart,
  candlesRequestOptions,
  getDataForPointsChart,
  getPriceArray,
  pricesLoading,
  isDataForChart,
};



