/**
 * Формирование на основе сид фразы адреса и приватного ключа кошелька
 *
 * @see [link](https://ethereum.stackexchange.com/questions/30824/what-is-the-easiest-way-to-convert-mnemonic-phrase-into-keystore-file)
 * @version 1.0.0
 */
import bip39 from 'bip39';
import hdkey from 'ethereumjs-wallet/hdkey';
import { getWeb3WithoutProvider } from '@/util/web3';

export default (seed) => {
  const hdwallet = hdkey.fromMasterSeed(bip39.mnemonicToSeed(seed));
  const walletHdpath = "m/44'/60'/0'/0/";

  const wallet = hdwallet.derivePath(walletHdpath + 0).getWallet();
  const address = getWeb3WithoutProvider().utils.toChecksumAddress(`0x${wallet.getAddress().toString('hex')}`);
  const privateKey = `0x${wallet.getPrivateKey().toString('hex').toUpperCase()}`;

  return {
    address,
    privateKey,
  };
};



