import { TIMEOUT_LEDGER_USER_RESPONSE, TIMEOUT_LEDGER_OPEN, TIMEOUT_LEDGER_SUPPORT_CHECK } from '@/config';
import log from '@/util/logger';
// import LedgerAppEth from '@ledgerhq/hw-app-eth';
import TransportU2F from '@ledgerhq/hw-transport-u2f';

export default async ({ exchangeTimeout = TIMEOUT_LEDGER_USER_RESPONSE, onCreated, getExchangeTimeout } = {}) => {
  let ledgerConnection;
  try {
    ledgerConnection = await TransportU2F.create(TIMEOUT_LEDGER_OPEN, TIMEOUT_LEDGER_SUPPORT_CHECK);
  } catch (e) {
    log.debug(`Error: Create Ledger TransportU2F: ${e}`);
    throw e;
  }
  if (onCreated !== undefined) {
    onCreated(ledgerConnection);
  }

  const timeout = getExchangeTimeout !== undefined
    ? getExchangeTimeout()
    : exchangeTimeout;
  ledgerConnection.setExchangeTimeout(timeout); // Ledger interaction timeouts (including user interactions)
  return ledgerConnection;
};



