<template>
  <transition
    name="transition-content-relative"
    mode="out-in"
    appear
  >
    <slot></slot>
  </transition>
</template>

<script>
export default {
  name: 'transition-content-relative',
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.transition-content-relative-enter-active, .transition-content-relative-leave-active {
  position: relative;
  transition: 0.3s;
  transition-property: transform, opacity;
  will-change: transform, opacity;
}

.transition-content-relative-enter, .transition-content-relative-leave-to {
  opacity: 0;
  transform: translateX(20%);
}
</style>



