import LedgerAppEth from '@ledgerhq/hw-app-eth';
import u2fFactory from './u2fFactory';

export default async (...args) => {
  try {
    return new LedgerAppEth(await u2fFactory(...args));
  } catch (e) {
    return null;
  }
};



