import getIntercom from '@/util/getIntercom';
import { INTERVAL_DESTROY_CHAT } from '@/config';

let componentsWithChatCount = 0;

const isChatEnabled = () => !window.intercomSettings.hide_default_launcher;

const hideChat = () => {
  if (isChatEnabled()) {
    window.intercomSettings.hide_default_launcher = true;
    getIntercom()('update', window.intercomSettings);
  }
};

export default {
  created() {
    this.$_eChat_enableCalled = false;
    this.$_eChat_vmDestroyed = false;
  },

  beforeDestroy() {
    if (this.$_eChat_enableCalled) {
      componentsWithChatCount -= 1;
      setTimeout(() => {
        if (componentsWithChatCount === 0) {
          hideChat();
        }
      }, INTERVAL_DESTROY_CHAT);
    }
    this.$_eChat_vmDestroyed = true;
  },

  methods: {
    $disableChat() {
      hideChat();
    },

    $enableChat() {
      if (!this.$_eChat_vmDestroyed) {
        if (!isChatEnabled()) {
          window.intercomSettings.hide_default_launcher = false;
          getIntercom()('update', window.intercomSettings);
        }
        componentsWithChatCount += 1;
        this.$_eChat_enableCalled = true;
      }
    },
  },
};



