<template>
  <div class="e-circle-icon"
  :class="[
    iconColor ? 'e-circle-icon_icon-' + iconColor : 'e-circle-icon_icon-default',
    background ? 'e-circle-icon_background-' + background : 'e-circle-icon_background-default',
    containerShift ? 'e-circle-icon_shifted' : null,
    size ? 'e-circle-icon_size-' + size : '',
    iconSize ? 'e-circle-icon_icon-format-' + iconSize : '',
    biggerOnTablet ? 'e-circle-icon_tablet-bigger' : '',
  ]">
    <e-icon
      :src="icon"
      :background="background"
      :iconColor="iconColor"
    />
  </div>
</template>

<script>
import EIcon from '@/components/EIcon';

export default {
  name: 'e-circle-icon',

  props: {
    icon: {
      type: String,
      default: '',
    },

    background: {
      type: String,
      default: 'default',
    },

    iconColor: {
      type: String,
      default: 'default',
    },

    containerShift: {
      type: Boolean,
      default: false,
    },

    size: {
      type: String,
      default: 'default',
    },

    iconSize: {
      type: String,
      default: null,
    },

    biggerOnTablet: {
      type: Boolean,
      default: false,
    },
  },

  components: {
    EIcon,
  },

};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-circle-icon {
  background-color: $color-white;
  border-radius: 50%;
  box-shadow: $box-shadow;
  height: 60px;
  min-width: 60px;
  width: 60px;

  .e-icon svg {
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 50%;
  }

  @include r(1200) {
    height: 50px;
    min-width: 50px;
    width: 50px;
  }

  &_tablet-bigger {
     @include r(1200) {
      height: 55px;
      min-width: 55px;
      width: 55px;
    }
  }

  &_icon_default {
    .e-icon svg {
      fill: $color-green;
    }
  }

  &_background-default {
    background-color: $color-white;
  }

  &_background-green {
    background-color: $color-green-bright;

    .e-icon svg {
      fill: $color-white;
    }
  }

  &_background-yellow {
    background-color: $color-yellow;

    .e-icon svg {
      fill: $color-ultramarine;
    }
  }

  &_background-red {
    background-color: $color-red;

    .e-icon svg {
      fill: $color-white;
    }
  }

  &_background-blue {
    .e-icon svg {
      fill: $color-blue;
    }
  }

  &_icon-format-lg {
    background-color: transparent;
    box-shadow: none;

    .e-icon {
      svg {
        width: 100%;
      }
    }
  }

  &_shifted {
    position: relative;
    left: -10px;

    @include r(1200) {
      left: -5px;
    }
  }

  &_size-large {
    height: 99px;
    width: 99px;

    @include r(1200) {
      height: 80px;
      width: 80px;
    }
  }
}
</style>




