<template>
  <div class="the-update-button">
    <div class="the-update-button__body">
      <div class="the-update-button__icon"><e-icon :src="iconRoundedArrow"></e-icon></div>
    </div>
  </div>
</template>

<script>
import iconRoundedArrow from '@/icons/e-icon_rounded_arrow.svg';

import EIcon from '@/components/EIcon';

export default {
  name: 'the-update-button',

  data() {
    return {
      iconRoundedArrow,
    };
  },
  components: {
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-update-button {
  &__body {
    border-radius: 50%;
    width: 99px;
    height: 99px;
    background-color: $color-yellow;
    box-shadow: 0 2px 3px rgba(black, .08);
    position: relative;
  }

  &__icon {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -17px;
    margin-left: -17px;
    width: 34px;
    height: 34px;
    border-radius: 50%;
    animation: rotating 2s linear infinite;
    backface-visibility: hidden;
    transform-origin: center center;

    .e-icon svg {
      overflow: visible;
    }
  }
}

@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

</style>



