/**
 * Формирование на основе keystore и приватного ключа кошелька
 *
 * @see [link](https://github.com/ethereumjs/ethereumjs-wallet/blob/master/src/test/index.js)
 * @version 1.0.0
 */

import Wallet from 'ethereumjs-wallet';
import { getWeb3WithoutProvider } from '@/util/web3';

export default (keyStore, pass) => {
  const wallet = Wallet.fromV3(keyStore, pass, true);
  const address = getWeb3WithoutProvider().utils.toChecksumAddress(`0x${wallet.getAddress().toString('hex')}`);
  const privateKey = `0x${wallet.getPrivateKey().toString('hex').toUpperCase()}`;

  return {
    address,
    privateKey,
  };
};



