/**
 * @module store/prices/helpers
 *
 * @version 1.0.3
 */

import { BigNumber } from 'bignumber.js';

/**
 * Дополняет пропущенные интервалы по свечками
 *
 * @param {array} - массив свечек, которые возвращает бекенд
 * @param {interval} - размер интеравала времени, по котором строятся свечки
 *
 * @since Version 1.0.3
 */
export const fillByCandles = (candles, interval, endDate) => {
  const res = [];
  if (candles.length > 0) {
    res.push(candles[0]);

    let nextDate = new Date(res[res.length - 1].time.getTime() + interval);
    let i = 1;

    while (nextDate < endDate) {
      const lastCandle = res[res.length - 1];
      if (i < candles.length) {
        // Случай когда в массиве переданном бекендом еще есть свечки
        if (nextDate.getTime() === candles[i].time.getTime()) {
          res.push(candles[i]);
          i += 1;
        } else {
          res.push({
            close: lastCandle.close,
            high: lastCandle.close,
            low: lastCandle.close,
            open: lastCandle.close,
            time: nextDate,
          });
        }
      } else {
        // Случай когда в массиве переданном бекендом уже не осталось свечек
        res.push({
          close: lastCandle.close,
          high: lastCandle.close,
          low: lastCandle.close,
          open: lastCandle.close,
          time: nextDate,
        });
      }

      nextDate = new Date(res[res.length - 1].time.getTime() + interval);
    }
  }

  return res;
};

/**
 * Функция
 *
 * @param {object} candle - свечка, которую модифицируем
 * @param {number} tradePrice - цена последнего треда
 * @param {Date} tradeTime - дата совершения трейда
 */
export const updateCandle = (candle, tradePrice) => ({
  close: tradePrice,
  high: Math.max(candle.high, tradePrice),
  low: Math.min(candle.low, tradePrice),
  open: candle.open,
  time: candle.time,
});

/**
 * Добавление трейда к кендлам
 *
 * @param {array} - Массив всех свечек, который есть на момент добавления трейда
 * @param {object} - Трейд
 * @param {number} - Длинна временного интервала
 *
 * @since Version 1.0.3
 */
export const addTradeToCandles = (candles, trade, interval) => {
  let res = [];
  const tradePrice = trade.price;
  if (candles.length === 0) {
    // Случай когда у нас вообще не было свечек и приходит первый трейд
    res = [{
      close: tradePrice,
      high: tradePrice,
      low: tradePrice,
      open: tradePrice,
      time: trade.createdAt,
    }];
  } else {
    // Случай когда были кендлы
    const lastCandle = candles[candles.length - 1];
    const timeDelta = trade.createdAt.getTime() - lastCandle.time.getTime();
    if (timeDelta <= interval) {
      if (timeDelta >= 0) {
        // Трейд попадает в последний кендл
        res = [...candles.slice(0, -1), updateCandle(lastCandle, tradePrice)];
      }
    } else {
      // Трейд не попадает в последний трейд
      let nextTime = new Date(lastCandle.time.getTime() + interval);
      while (nextTime < trade.createdAt) {
        res.push({
          close: lastCandle.close,
          high: lastCandle.close,
          low: lastCandle.close,
          open: lastCandle.close,
          time: nextTime,
        });
        nextTime = new Date(nextTime.getTime() + interval);
      }
      res[res.length - 1].close = tradePrice;
      res[res.length - 1].high = tradePrice;
      res[res.length - 1].low = tradePrice;
      res[res.length - 1].open = tradePrice;
      res = [...candles, ...res];
    }
  }

  return res;
};

/**
 * Функция формрованний объекта для рендера графика содержащего набор данных, первую и последнюю точки
 *
 * @param {array} dataSet - набор данных, по которому формируется массив для отрисовки
 *
 * @since Version 1.0.2
 *
 */
export const makeObjectForChart = (dataSet) => {
  let res = {
    dataSet: [],
    first: {
      price: null,
      priceDiff: null,
      priceDiffInPercent: null,
    },
    last: {
      price: null,
      priceDiff: null,
      priceDiffInPercent: null,
    },
  };

  if (dataSet.length > 1) {
    res = {
      // Данные для построения графика
      dataSet,

      // Данные в первой точке графика
      first: {
        price: dataSet[dataSet.length - 1].price,
        time: dataSet[dataSet.length - 1].time,
      },

      // Данные в последней точке график
      last: {
        price: dataSet[0].price,
        priceDiff: dataSet[0].price - dataSet[dataSet.length - 1].price,
        priceDiffInPercent: dataSet.length > 1 && dataSet[0].price && dataSet[dataSet.length - 1].price
          ? ((dataSet[0].price - dataSet[dataSet.length - 1].price) / dataSet[dataSet.length - 1].price) * 100
          : 0,
        time: dataSet[0].time,
      },
    };
  }

  return res;
};

/**
 * Вычисление среднего арифметического значений на интервале
 *
 * @private
 *
 * @since Version 1.0.2
 */
export const average = (dataSet) => {
  let sumOfVolumes = BigNumber(0);
  let sumOfPrices = BigNumber(0);

  for (let i = 0; i < dataSet.length; i += 1) {
    sumOfVolumes = sumOfVolumes.plus(dataSet[i].volume);
    sumOfPrices = sumOfPrices.plus(dataSet[i].volume.times(dataSet[i].price));
  }

  return sumOfPrices.div(sumOfVolumes).toNumber();
};

/**
 * Функция мепит значения на среднее значение
 *
 * @private
 *
 * @since Version 1.0.2
 */
export const mapToAverage = (dataSet) => {
  // eslint-disable-next-line
  for (const key of Object.keys(dataSet)) {
    // eslint-disable-next-line
    dataSet[key] = average(dataSet[key]);
  }

  return dataSet;
};

/**
 * Функция преобразует массив свечек в данные для отборажения на графике
 *
 * @since Version 1.0.4
 */

export const mapCandlesForChart = candles => candles.map(item => ({
  price: item.close,
  time: item.time,
}));


/**
 * Helper: Инвертирует массив с ценами
 *
 * @param {array} - Массив который нужно инвертировать
 * @return {array} - инвертированный массив
 *
 * @since Version 1.0.3
 */

export const invertPriceArray = pricesArray => pricesArray.reverse();



