import { TIMEOUT_LEDGER_USER_RESPONSE, TIMEOUT_LEDGER_GET_ACCOUNTS } from '@/config';
import { LedgerSubprovider } from '@0xproject/subproviders';
import { Lock } from 'semaphore-async-await';
import EventEmitter from 'eventemitter3';
import withTimeout from '@/util/withTimeout';
import ledgerEthereumClientFactoryAsync from './ledgerEthereumClientFactoryAsync';

const DEFAULT_ACCOUNTS_COUNT = 1;

/**
 * setEngine - web3 SubProvider method
 * handleRequest - web3 SubProvider method
 * emitPayload - NOT IMPLEMENTED web3 SubProvider method
 * emitPayloadAsync - 0xproject altered version of emitPayload
 *
 * RPC methods:
 * getAccountsAsync
 * signTransactionAsync
 * signPersonalMessageAsync
 * signTypedDataAsync - NOT IMPLEMENTED
 */
export default class ELedgerSubprovider extends LedgerSubprovider {
  constructor(config) {
    super(config);
    this.initConfig(config);
    this.ledgerConnectionLock = new Lock();
    this.emitter = new EventEmitter();
  }

  /* eslint-disable no-underscore-dangle */
  /**
   * Fixes non-released lock in original implementation.
   * @returns {Promise<void>}
   * @private
   */
  async _createLedgerClientAsync() {
    try {
      return await super._createLedgerClientAsync();
    } finally {
      // eslint-disable-next-line no-underscore-dangle
      this._connectionLock.release();
    }
  }

  /**
   * Fixes non-released lock in original implementation.
   * @returns {Promise<void>}
   * @private
   */
  async _destroyLedgerClientAsync() {
    await this._connectionLock.acquire();
    if (this._ledgerClientIfExists === undefined) {
      this._connectionLock.release();
      return;
    }
    try {
      await this._ledgerClientIfExists.transport.close();
    } finally {
      this._ledgerClientIfExists = undefined;
      this._connectionLock.release();
    }
  }
  /* eslint-enable no-underscore-dangle */

  initConfig(config) {
    const accountFetchingConfigs = config.accountFetchingConfigs;
    this.defaultAccountsCount = accountFetchingConfigs !== undefined
      ? accountFetchingConfigs.numAddressesToReturn
      : DEFAULT_ACCOUNTS_COUNT;
    this.ledgerEthereumClientFactoryAsync = config.ledgerEthereumClientFactoryAsync;
  }

  async getAccountsAsync(accountsCount = this.defaultAccountsCount) {
    await this.ledgerConnectionLock.acquire();
    // eslint-disable-next-line no-underscore-dangle
    this._ledgerEthereumClientFactoryAsync = ledgerEthereumClientFactoryAsync.bind(null, {
      exchangeTimeout: TIMEOUT_LEDGER_GET_ACCOUNTS,
    });
    try {
      return await withTimeout(super.getAccountsAsync(accountsCount), TIMEOUT_LEDGER_GET_ACCOUNTS + 1);
    } catch (e) {
      await this.afterErrorThrown(e);
      throw e;
    } finally {
      this.afterSessionFinished();
    }
  }

  async signTransactionAsync(...args) {
    await this.ledgerConnectionLock.acquire();
    // eslint-disable-next-line no-underscore-dangle
    this._ledgerEthereumClientFactoryAsync = ledgerEthereumClientFactoryAsync.bind(null, {
      exchangeTimeout: TIMEOUT_LEDGER_USER_RESPONSE,
    });
    try {
      return await withTimeout(super.signTransactionAsync(...args), TIMEOUT_LEDGER_USER_RESPONSE + 1);
    } catch (e) {
      await this.afterErrorThrown(e);
      throw e;
    } finally {
      this.afterSessionFinished();
    }
  }

  async signPersonalMessageAsync(...args) {
    await this.ledgerConnectionLock.acquire();
    // eslint-disable-next-line no-underscore-dangle
    this._ledgerEthereumClientFactoryAsync = ledgerEthereumClientFactoryAsync.bind(null, {
      exchangeTimeout: TIMEOUT_LEDGER_USER_RESPONSE,
    });
    try {
      return await withTimeout(super.signPersonalMessageAsync(...args), TIMEOUT_LEDGER_USER_RESPONSE + 1);
    } catch (e) {
      await this.afterErrorThrown(e);
      throw e;
    } finally {
      this.afterSessionFinished();
    }
  }

  /**
   * 0xproject extension
   * @param args
   */
  emitPayloadAsync(...args) {
    return super.emitPayloadAsync(...args);
  }

  /**
   * Web3 method
   * @param args
   */
  setEngine(...args) {
    return super.setEngine(...args);
  }

  /**
   * Web3 method
   * @param args
   */
  handleRequest(...args) {
    return super.handleRequest(...args);
  }

  async afterErrorThrown() {
    try {
      // eslint-disable-next-line no-underscore-dangle
      await this._destroyLedgerClientAsync();
    } catch (destroyErr) {
      // do nothing
    }
  }

  /**
   * @private
   * @returns {Promise<void>}
   */
  afterSessionFinished() {
    this.ledgerConnectionLock.release();
  }
}



