import {
  APP_ETH_NETWORK_ID,
} from '@/config';
import LedgerSubprovider from './ELedgerSubprovider';
import ledgerEthereumClientFactoryAsync from './ledgerEthereumClientFactoryAsync';

let cache;

export default () => {
  if (!cache) {
    cache = new LedgerSubprovider({
      networkId: APP_ETH_NETWORK_ID,
      ledgerEthereumClientFactoryAsync,
      accountFetchingConfigs: {
        // addressSearchLimit: 1,
        numAddressesToReturn: 1,
      },
    });
  }
  return cache;
};



