/**
 * @module store/contracts/getters
 * @version 1.0.2
 */


/**
 * Getter: Возвращает список доступных версий главного контракта
 *
 * @returns {array}
 * @since Version 1.0.1
 */
const exchangeContracts = (state) => {
  let contracts = [...state.exchangeContracts];
  if (contracts.length > 0) {
    contracts = contracts.map((item, index) => ({ ...item, isCurrent: index === 0 }));
  }
  return contracts;
};


/**
 * Getter: Возвращает активную версию главного контракта
 *
 * @returns {object | null}
 * @since Version 1.0.1
 */
const activeMainContract = state => state.activeMainContract;

/**
 * Getter: Возвращает адрес активной версии главного контракта
 *
 * @returns {string}
 * @since Version 1.0.2
 */
const activeMainContractAddress = state => state.activeMainContract && state.activeMainContract.contractAddress;
/**
 * Getter: Возвращает:
 * true если активна последняя версия контракта
 * false если активна не последняя версия контракта
 * null - если список контрактов ещё не загружен
 *
 * @returns {boolean|null}
 * @since Version 1.0.1
 */
const isLatestExchangeContractActive = (state) => {
  const active = state.activeMainContract;
  const latest = state.exchangeContracts[0]; // latest is first
  return !!(active && latest && active.version === latest.version);
};

const affiliateContract = state => state.affiliateContract;

export default {
  exchangeContracts,
  activeMainContract,
  activeMainContractAddress,
  isLatestExchangeContractActive,
  affiliateContract,
};



