<template>
  <div
    class="e-catalogue-table-item"
    @click="onClick"
  >
    <div class="e-catalogue-table-item__column e-catalogue-table-item__token">
      <div class="e-catalogue-table-item__token-index e-catalogue-table-item__text">{{ index }}</div>
      <e-token-icon
        :contract="tokenContract"
        class="e-catalogue-table-item__token-img"
      />
      <div class="e-catalogue-table-item__token-data">
        <div class="e-catalogue-table-item__token-symbol-wrap">
          <div class="e-catalogue-table-item__token-symbol">{{ tokenAbbr }}</div>
          <div
            v-if="isLogin"
            class="e-catalogue-table-item__star"
            :class="{ 'e-catalogue-table-item__star_my': isMy }"
            @click.stop="toggleTokenFavorite"
          >
            <e-svg-from-store icon="star"/>
          </div>
        </div>
        <div class="e-catalogue-table-item__token-name e-catalogue-table-item__text">{{ tokenName }}</div>
      </div>
    </div>

    <div class="e-catalogue-table-item__column e-catalogue-table-item__price">
      <e-big-number
        :value="price"
        :decimal-places="8"
      />
      <e-svg-from-store icon="earned"/>
    </div>

    <div class="e-catalogue-table-item__column e-catalogue-table-item__eth-value">
      <e-big-number
        :value="volume24"
        :decimals="ETH_DECIMALS"
        :decimal-places="3"
      />
      <e-svg-from-store icon="earned"/>
    </div>

    <div class="e-catalogue-table-item__column e-catalogue-table-item__eth-value">
      <template v-if="marketcap">
        <e-big-number
          :value="marketcap"
          :decimals="ETH_DECIMALS"
          :decimal-places="3"
        />
        <e-svg-from-store icon="earned"/>
      </template>
    </div>

    <div
      class="e-catalogue-table-item__column e-catalogue-table-item__change"
      :class="increasing ? 'e-catalogue-table-item__change_up' : 'e-catalogue-table-item__change_down'"
    >
      <div class="e-catalogue-table-item__change-inner">
        <div class="e-catalogue-table-item__graph-placeholder">
          <!-- Пока скрываем гарфики для оптимизации. Потому нужно будет хорошо протестирвоать, что именно тормозит отрисовку каталога -->
          <!-- <e-small-price-chart
            :v-if="candles24h.length > 1"
            :dataSet="candles24h"
            :chartType="increasing ? 'increase' : 'decrease'"
          /> -->
        </div>
        <e-big-number
          :value="priceDiff24InPercent"
          :decimals="0"
          :decimal-places="2"
          sign
        />%

        <div class="e-catalogue-table-item__pinner e-catalogue-table-item__pinner_link">
          <div
            class="e-catalogue-table-item__pinner-icon"
            @click.stop="openLink(`${ETHERSCAN_URL}/token/${tokenContract}`)"
          >
            <e-svg-from-store icon="websiteLink"/>
          </div>
          <e-hint-adress
            class="e-catalogue-table-item__popover"
            title="Token Url"
            :side="popoverTop ? 'top-right' : 'right'"
            :adress="tokenContract"
          />
        </div>

        <div class="e-catalogue-table-item__pinner e-catalogue-table-item__pinner_hint-right">
          <!-- BEGIN: Случай когда у токена есть URL -->
          <template v-if="websiteUrl">
            <a
              :href="websiteUrl"
              target="_blank"
              class="e-catalogue-table-item__website"
              :class="{
                  'e-catalogue-table-item__website_verified': isWebsiteVerified,
                  'e-catalogue-table-item__website_unverified': !isWebsiteVerified,
                }"
              @click.stop
            >
              <div class="e-catalogue-table-item__pinner-icon">
                <e-svg-from-store icon="world"/>
              </div>
            </a>

            <div class="e-catalogue-table-item__hint">
              <div class="e-catalogue-table-item__hint-title">{{verifiedString}}</div>
              <a
                :href="websiteUrl"
                target="_blank"
                class="e-catalogue-table-item__website"
                :class="{
                  'e-catalogue-table-item__website_verified': isWebsiteVerified,
                  'e-catalogue-table-item__website_unverified': !isWebsiteVerified,
                }"
                @click.stop
              >
                {{websiteUrl}}
              </a>
            </div>
          </template>
          <!-- END: Случай когда у токена есть URL -->

          <!-- BEGIN: Случай когда у токена нет URL -->
          <template v-else>
            <div class="e-catalogue-table-item__website e-catalogue-table-item__website_unverified">
              <div class="e-catalogue-table-item__pinner-icon">
                <e-svg-from-store icon="world"/>
              </div>
            </div>
          </template>
          <!-- END: Случай когда у токена нет URL -->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { BigNumber } from 'bignumber.js';
import { mapGetters } from 'vuex';
import openLink from '@/util/openLink';

import EHintAdress from '@/components/EHintAdress';
import ETokenIcon from '@/components/ETokenIcon';
import EBigNumber from '@/components/EBigNumber';
import ESmallPriceChart from '@/components/ESmallPriceChart';
import ESvgFromStore from '@/components/ESvgFromStore';

export default {
  name: 'e-catalogue-table-item',

  props: {
    index: {
      type: Number,
      default: 0,
    },

    tokenContract: {
      type: String,
      required: true,
    },

    tokenName: {
      type: String,
      default: null,
    },

    tokenAbbr: {
      type: String,
      default: null,
    },

    decimals: {
      type: Number,
      default: null,
    },

    price: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    volume24: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    volume: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    // Изменение цены за 24 часа в процентах
    priceDiff24InPercent: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    // Выросла цена за 24 часа или нет
    increasing: {
      type: Boolean,
      default: false,
    },

    // Массив с данными для отрисовки графика изменения цен за последние 24 часа
    // candles24h: {
    //   type: Array,
    //   default: null,
    // },

    marketcap: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    // Является ли токен "моим" true - да, false - нет
    isMy: {
      type: Boolean,
      default: false,
    },

    websiteUrl: {
      type: String,
      default: null,
    },

    isWebsiteVerified: {
      type: Boolean,
      default: false,
    },

    popoverTop: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    ...mapGetters({
      isLogin: 'wallets/isLogin',
    }),

    verifiedString() {
      return (this.isWebsiteVerified ? 'Verified Website' : 'Unverified Website');
    },
  },

  methods: {
    toggleTokenFavorite() {
      this.$store.dispatch('tokens/toggleTokenFavorite', this.tokenContract);
    },

    onClick() {
      this.$router.push({ name: 'token', params: { token: this.tokenContract } });
    },

    openLink,
  },


  components: {
    EHintAdress,
    ESvgFromStore,
    EBigNumber,
    ESmallPriceChart,
    ETokenIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-catalogue-table-item {
  $this: &;

  display: flex;
  align-items: center;
  cursor: pointer;
  box-sizing: border-box;
  font-size: 17px;
  height: 60px;
  border-radius: 3px;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.08);
  background: #ffffff;
  transition: $transition-time;

  &:hover {
    background-color: #F3F6FF;
    box-shadow: 0 5px 5px rgba(0, 0, 0, 0.08);
  }

  @include r(1200) {
    font-size: 14px;
  }

  &__column {
    flex-basis: 19.5%;
    max-width: 19.5%;
    min-width: 19.5%;
    box-sizing: border-box;
    padding-left: 20px;
    display: flex;
    align-items: center;
    justify-content: flex-end;

    &:last-of-type {
      padding-right: 22px;
    }

    @include r(1300) {
      flex-basis: 17.5%;
      max-width: 17.5%;
      min-width: 17.5%;
    }
  }

  &__token {
    height: 100%;
    display: flex;
    align-items: center;
    padding: 15px 0 15px 0;
    line-height: 1;
    justify-content: flex-start;

    .e-tocken-icon {
      width: 30px;
      height: 30px;
      min-width: 30px;
      min-height: 30px;
      margin-right: 8px;
    }
  }

  &__token-symbol {
    margin-top: 2px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: 14px;
    color: #000;
    max-width: 100%;
  }

  &__token-symbol-wrap {
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }

  &__token-index {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 37px;
  }

  &__text {
    font-weight: 600;
    font-size: 10px;
    letter-spacing: 0.5px;
    color: $color-grey;
  }

  &__popover {
    z-index: 2;
  }

  &__token-name {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  &__token-data {
    overflow: hidden;
  }

  &__star {
    margin-left: 5px;

    .e-svg-from-store {
      cursor: pointer;
      width: 12px;
      height: 12px;
    }

    .e-svg-from-store__svg {
      transition: fill $transition-time;
      fill: $color-grey-second;
    }

    &:hover .e-svg-from-store__svg {
      fill: $color-blue;
    }
  }

  &__star_my {
    .e-svg-from-store__svg {
      fill: #FF9800;
    }
  }

  &__price {
    color: #09168a;
    font-size: 13px;
    font-weight: 600;

    .e-big-number__leading-zeros {
      color: rgba(9, 22, 138, 0.5);
    }

    .e-svg-from-store {
      height: 10px;
      margin-left: 2px;
      margin-top: -2px;
      margin-right: -2px;
      width: 10px;
    }

    .e-svg-from-store__svg {
      fill: $color-blue;
    }
  }

  &__eth-value {
    color: $color-dark-blue;
    font-size: 11px;
    line-height: 1;
    font-weight: 600;

    .e-svg-from-store {
      height: 10px;
      width: 10px;
      margin-right: -1px;

      &__svg {
        fill: $color-dark-blue;
        margin-left: 2px;
      }
    }
  }

  &__change {
    position: relative;
    font-size: 11px;
    font-weight: 600;
    flex-basis: 22%;
    max-width: 22%;

    @include r(1300) {
      flex-basis: 31%;
      max-width: 31%;
    }
  }

  &__change-inner {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;

    .e-big-number {
      margin-left: 5px;
      min-width: 75px;
      text-align: right;
    }
  }

  &__change_up {
    color: $color-green-bright;
  }

  &__change_down {
    color: $color-red;
  }

  &__graph-placeholder {
    width: 66px;
    height: 24px;
    margin: auto;
  }

  &__pinner {
    position: relative;
    padding: 0 6px;
    box-sizing: border-box;
    display: flex;
    align-items: center;

    &.is-active {
      #{$this}__hint {
        opacity: 1;
        visibility: visible;
      }
    }

    &:hover {
      #{$this}__hint {
        opacity: 1;
        visibility: visible;
      }

      .e-hint-adress {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__pinner_link {
    top: 1px;

    #{$this}__pinner-icon svg {
      fill: #CFCFDE;
    }
  }

  &__pinner_hint-right {
    #{$this}__hint {
      top: calc(100% + 3px);
      right: 0;
    }
  }

  &__pinner-icon {
    width: 19px;
    height: 19px;
    display: flex;
    align-items: center;

    svg {
      transition: $transition-time;
      transform: translate3d(0,0,0);
    }
  }

  // Подтвержденный сайт токена
  &__website_verified &__pinner-icon svg {
    fill: #1FCE43;
  }

  // Неподтвержденный сайт токена
  &__website_unverified &__pinner-icon svg {
    fill: $color-red;
  }


  &__hint {
    position: absolute;
    top: -8px;
    opacity: 0;
    transition: $transition-time;
    visibility: hidden;
    background-color: #fff;
    border-radius: 3px;
    box-shadow: 0 3px 10px rgba(#94A5B4, .14);
    box-sizing: border-box;
    min-height: 38px;
    min-width: 106px;
    text-align: center;
    padding: 5px 10px 2px;
  }

  &__hint-title {
    font-size: 10px;
    font-weight: 400;
    color: #94A5B4;
    line-height: 1;
    margin-bottom: 3px;
  }

  &__website {
    font-weight: 700;
    font-size: 11px;
    display: inline-block;
    text-decoration: none;
    line-height: 1;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 150px;
    color: $color-green;
    transition: $transition-time;
    position: relative;

    &:hover {
      color: $color-blue;
    }
  }

  &__website_verified {
    color: $color-green;
  }

  &__website_unverified {
    color: $color-red;
  }
}
</style>



