<template>
  <div class="the-wallet-import-mnemonic-phrase">
    <div class="the-wallet-import-mnemonic-phrase__container">
      <form class="the-wallet-import-mnemonic-phrase__form" action="#">
        <div class="the-wallet-import-mnemonic-phrase__inputbox">
          <e-text-area-validated
            v-model="phrase"
            :vuelidate-property="$v.phrase"
            label="Your mnemonic phrase..."
            :icon="iconKeySecret"
          />
        </div>
        <!-- <div class="the-wallet-import-mnemonic-phrase__inputbox-password">
          <e-text-input-validated
            v-model="password"
            :vuelidate-property="$v.password"
            type="password"
            label="Password"
            :icon="iconSecretLock"
          />
        </div> -->
        <div class="the-wallet-import-mnemonic-phrase__sbmt">
          <e-button
            fill="green"
            width="s"
            :icon="iconWalletImport"
            :disabled="!isReadyToSubmit"
            @click="importWallet"
          >
            Import wallet
          </e-button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
/**
 * Component: Компонент импорта кошелька из формата мнемонической фразы
 *
 * @version 1.0.0
 */
import { validationMixin } from 'vuelidate';
import { required } from 'vuelidate/lib/validators';

import generateAddressesFromSeed from '@/util/generateAddressesFromSeed';

import { routesMixin } from '@/util/mixins';

import { PRIVATE_KEY_TYPE } from '@/util/constants';

// Components
import ETextAreaValidated from '@/components/ETextAreaValidated';
import ETextInputValidated from '@/components/ETextInputValidated';
import EButton from '@/components/EButton';

// Icons
import iconKeySecret from '@/icons/e-icon__key-secret.svg';
import iconSecretLock from '@/icons/e-icon_secret-lock.svg';
import iconWalletImport from '@/icons/e-icon_wallet_import.svg';

export default {
  name: 'the-wallet-import-mnemonic-phrase',

  data() {
    return {
      address: '',
      privateKey: '',
      phrase: '',
      password: '',
      iconKeySecret,
      iconSecretLock,
      iconWalletImport,
    };
  },

  validations: {
    password: {},
    phrase: {
      required,
      has12Words(phrase) {
        return phrase.trim().split(/\s+/g).length >= 12;
      },
    },
  },

  computed: {
    isReadyToSubmit() {
      return !this.$v.$invalid;
    },
  },

  methods: {
    /**
     * Функция импорта кошелька по мнемонической фразе
     */
    async importWallet() {
      const wallet = generateAddressesFromSeed(this.phrase);

      if (!this.$store.getters['wallets/hasPrivateKeyWallet'](wallet.address)) {
        await this.$store.dispatch('wallets/importWallet', {
          address: wallet.address,
          privateKey: wallet.privateKey,
          type: PRIVATE_KEY_TYPE,
        });

        // Отправка аналитики по импорту кошелька
        this.$ga.page({
          page: '/virtual/import',
          title: 'Wallet Was Imported',
          location: window.location.href,
        });
        this.goToTrading();
      } else {
        this.$store.dispatch('wallets/setActiveWallet', {
          address: wallet.address,
          type: PRIVATE_KEY_TYPE,
        });
        this.goToTrading();
      }
    },

    goToTrading() {
      const activeTokenAddress = this.$store.getters['tokens/activeTokenContract'];
      if (activeTokenAddress) {
        this.$router.push({
          name: 'token',
          params: { token: activeTokenAddress },
        });
      } else {
        this.$router.push({ name: 'tokens' });
      }
    },
  },

  components: {
    ETextAreaValidated,
    ETextInputValidated,
    EButton,
  },

  mixins: [validationMixin, routesMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-wallet-import-mnemonic-phrase {
  &__container {
    margin-top: 20px;
    max-width: 625px;

    @include r(1200) {
      max-width: 439px;
    }
  }

  &__inputbox {
    position: relative;
    margin-bottom: 30px;
    margin-left: -50px;

    @include r(1200) {
      margin-bottom: 20px;
    }

    .e-text-area__box {
      width: 475px;
      height: 100px;
    }

    .e-text-area__label {
      color: rgba(1,0,146,.4);
    }
  }

  &__inputbox-password {
    position: relative;
    margin-bottom: 30px;
    margin-left: -50px;

    @include r(1200) {
      margin-bottom: 20px;
    }
  }

  &__upload {
    width: 250px;
    margin-bottom: 30px;
  }

  .e-upload-files {
    background-color: transparent;
  }

  .e-upload-files__upload {
    background: #fff;
  }
}
</style>




