import io from 'socket.io-client';
import ioReq from 'socket.io-request';
import { BACKEND_URL, TIMEOUT_BACKEND_REQUEST } from '@/config';
import axios from 'axios';

const http = axios.create({
  baseURL: BACKEND_URL,
});

let socket = null;

const getSocket = () => {
  if (!socket) {
    socket = io(BACKEND_URL, {
      reconnection: true,
      reconnectionDelay: 1000,
      reconnectionDelayMax: 5000,
      reconnectionAttempts: Infinity,
    });
  }
  return socket;
};

let ioReqSocket = null;

const getIoReqSocket = () => {
  if (!ioReqSocket) {
    ioReqSocket = ioReq(getSocket(), {
      timeout: TIMEOUT_BACKEND_REQUEST,
    });
  }
  return ioReqSocket;
};

export default {
  get socket() {
    return getSocket();
  },

  wsRequest: (...args) => getIoReqSocket().request(...args),

  http,
};



