<template>
  <div class="the-history">
    <transition-content>
      <div class="the-history__wrapper">
        <e-button-tabs
          :active-tab.sync="activeTab"
          :tabs="ALL_TABS"
          class="the-history__tabs"
        />

        <the-history-table
          :history="historyPage"
          class="the-history__table"
          :class="{ 'is-loading': historyPageLoading }"
        />

        <e-pagination
          v-if="pageCount > 1"
          v-model="pageNumber"
          slot="footer"
          :page-count="pageCount"
          class="the-history__pagination"
        />

        <!-- Прелоудер который отображается на во время загрузки данных -->
        <div
          class="the-history__preloader"
          v-if="historyPageLoading"
        >
          <e-preloader />
        </div>
      </div>
    </transition-content>
  </div>
</template>

<script>
/**
 * Component: Отображение страницы истории транзакций пользователя
 *
 * @version 1.0.2
 */
import { mapState, mapGetters, mapActions } from 'vuex';
import { SECTION_FUNDS, SECTION_TRADES, SECTION_AFFILIATES } from '@/api/backend/constants';

// Components
import EButtonTabs from '@/components/EButtonTabs';
import EIcon from '@/components/EIcon';
import EPagination from '@/components/EPagination';
import EPreloader from '@/components/EPreloader';
import TheHistoryTable from '@/containers/blocks/TheHistoryTable';

// Transitions
import TransitionContent from '@/containers/transitions/TransitionContent';

// Constants
const TAB_ALL = 'All';
const TAB_TRADING = 'Trading';
const TAB_FUNDS = 'Funds';
const TAB_AFFILIATE = 'Affiliate';

const TAB_TO_SECTION = {
  [TAB_TRADING]: SECTION_TRADES,
  [TAB_FUNDS]: SECTION_FUNDS,
  [TAB_AFFILIATE]: SECTION_AFFILIATES,
};

const ALL_TABS = [TAB_ALL, TAB_TRADING, TAB_FUNDS, TAB_AFFILIATE];

export default {
  name: 'the-history',

  data() {
    return {
      activeTab: TAB_ALL,
    };
  },

  computed: {
    ...mapState({
      historyPage: state => state.history.activePage,
      historyPageLoading: state => state.history.historyPageLoading,
      pageCount: state => state.history.numberOfPages,
    }),
    ...mapGetters({
      userAddress: 'wallets/activeWallet',
    }),

    pageNumber: {
      get: vm => vm.$store.state.history.activePageNumber,
      set(pageNumber) {
        this.fetchPage(pageNumber);
      },
    },
  },

  watch: {
    activeTab: 'fetchFirstPage',
    userAddress: 'fetchFirstPage',
  },

  created() {
    this.ALL_TABS = ALL_TABS;
    this.fetchFirstPage();
  },

  methods: {
    ...mapActions({
      fetchHistory: 'history/getHistory',
    }),

    fetchFirstPage() {
      this.fetchPage(0);
    },

    fetchPage(pageNumber) {
      this.fetchHistory({
        pageNumber,
        section: TAB_TO_SECTION[this.activeTab],
      });
    },
  },

  components: {
    EButtonTabs,
    EIcon,
    EPagination,
    EPreloader,
    TheHistoryTable,
    TransitionContent,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-history {
  margin: -46px 0 22px 0;
  height: calc(100% + 24px);

  &__wrapper {
    height: 100%;
    display: flex;
    flex-flow: column;
  }

  &__table {
    min-width: 800px;
    flex: 1;
    transition: opacity 0.5s ease-in-out;

    &.is-loading {
      opacity: 0.2;
    }
  }

  &__tabs {
    margin-bottom: 18px;
    flex: none;
  }

  &__pagination {
    margin-top: 24px;
    margin-bottom: 28px;
    flex: none;
  }

  &__preloader {
    position: absolute;
    top: 0;
    left: 0;
    right: 10px;
    bottom: 0;
  }
}
</style>



