import backend from '@/api/backend/endpoint';
import { ORDERS_PAGE_SIZE } from '@/config';

export const getOrderHistoryPage = async ({ userAddress, contractAddress, pageNumber }) => {
  const { data, meta } = await backend.wsRequest('/orders/get', {
    filters: {
      // token_address: tokenAddress,
      user_address: userAddress,
      contract_address: contractAddress,
      is_history: true,
    },
    limit: ORDERS_PAGE_SIZE,
    offset: pageNumber * ORDERS_PAGE_SIZE,
  });

  return {
    data,
    total: meta.count_items,
  };
};

export const getActiveOrdersPage = async ({ userAddress, contractAddress, pageNumber }) => {
  const { data, meta } = await backend.wsRequest('/orders/get', {
    filters: {
      // token_address: tokenAddress,
      user_address: userAddress,
      contract_address: contractAddress,
      is_canceled: false,
      is_fulfilled: false,
    },
    limit: ORDERS_PAGE_SIZE,
    offset: pageNumber * ORDERS_PAGE_SIZE,
  });

  return {
    data,
    total: meta.count_items,
  };
};



