<template>
  <div class="the-wallet-pages-popup">
    <div class="the-wallet-pages-popup__container">
      <transition-content>
        <div class="the-wallet-pages-popup__generating-icon">
          <div class="the-wallet-pages-popup__generate">
            <div class="the-wallet-pages-popup__generate-inner">
              <div class="the-wallet-pages-popup__generate-icon">
                <e-icon :src="iconGeneration"/>
              </div>
              <div class="the-wallet-pages-popup__generate-title">
                generating credentials<span class="dot">.</span><span class="dot">.</span><span class="dot">.</span>
              </div>
            </div>
          </div>
        </div>
      </transition-content>
    </div>
  </div>
</template>

<script>
import { ETH_ADDRESS } from '@/util/constants';

/**
 * Component: Страница генерации кошелька
 *
 * @version 1.0.2
 */
import { POPUP_MY_WALLET } from '@/store/constants';
import { routesMixin } from '@/util/mixins';

import EIcon from '@/components/EIcon';

import iconGeneration from '@/icons/e-icon_generation.svg';

import TransitionContent from '@/containers/transitions/TransitionContent';

export default {
  name: 'the-wallet-pages-popup',

  data() {
    return {
      destroyed: false,
    };
  },

  created() {
    Object.assign(this, {
      iconGeneration,
    });
  },

  mounted() {
    if (this.destroyed) return;
    setTimeout(async () => {
      if (this.destroyed) return;
      this.$store.dispatch('wallets/createWallet');
      this.$router.push(this.$popupRoute({ popupPage: POPUP_MY_WALLET, walletToken: ETH_ADDRESS }));
    }, 2000);
  },

  destroyed() {
    this.destroyed = true;
  },

  components: {
    EIcon,
    TransitionContent,
  },

  mixins: [routesMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-wallet-pages-popup {
  &__container {
    height: 100%;
  }

  &__generate {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 1;
    height: calc(100vh - 200px);

    #e-icon_generation__arrow path {
      transform-origin: 10.2px 14px 0;
      animation: rotating 1.2s linear infinite;
    }

    .dot {
      animation: dottes 1.4s linear infinite;
      opacity: 0;

      &:nth-child(2) {
        animation-delay: 0.2s;
      }

      &:nth-child(3) {
        animation-delay: 0.4s;
      }
    }
  }

  &__generate-inner {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &__generate-title {
    font-size: 16px;
    color: $color-dark-blue;
    font-weight: 400;
  }

  &__generate-icon {
    height: 110px;
    width: 156px;
  }

  @keyframes rotating {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }

  @keyframes dottes {
    0% {
      opacity: 1;
    }
    70%, 100% {
      opacity: 0;
    }
  }
}
</style>




