<template>
  <div class="the-wallet-import-keystore">
    <div class="the-wallet-import-keystore__container">
      <form class="the-wallet-import-keystore__form" action="#">
        <div class="the-wallet-import-keystore__upload">
          <e-upload-files
            accept="*.*"
            acceptLabel="*.*"
            label="Open Keystore file"
            small-design
            class="the-wallet-import-keystore__file-upload"
            @change="onFileUpload"
          />
        </div>
        <div class="the-wallet-import-keystore__inputbox-password">
          <e-text-input-validated
            v-model="password"
            :vuelidate-property="$v.password"
            type="password"
            label="Password"
            :icon="iconSecretLock"
          />
        </div>
        <div class="the-wallet-import-keystore__sbmt">
          <e-button
            fill="green"
            width="s"
            :icon="iconWalletImport"
            :disabled="!isReadyToSubmit"
            @click="importWallet"
          >
            Import wallet
          </e-button>
        </div>

        <div class="the-wallet-import-keystore__error"
          v-if="error !== ''"
        >
          {{error}}
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import { validationMixin } from 'vuelidate';
import { required } from 'vuelidate/lib/validators';

import { routesMixin } from '@/util/mixins';
import { PRIVATE_KEY_TYPE } from '@/util/constants';

import generateAddressesFromKeyStore from '@/util/generateAddressesFromKeyStore';

// Components
import ETextInputValidated from '@/components/ETextInputValidated';
import EButton from '@/components/EButton';
import EUploadFiles from '@/components/EUploadFiles';

// Icons
import iconSecretLock from '@/icons/e-icon_secret-lock.svg';
import iconWalletImport from '@/icons/e-icon_wallet_import.svg';

export default {
  name: 'the-wallet-import-keystore',

  data() {
    return {
      file: null,
      keyStore: null,
      password: '',
      error: '',
      iconSecretLock,
      iconWalletImport,
    };
  },

  validations: {
    password: {
      required,
    },

    keyStore: {
      required,
      notNull: v => v !== null,
    },
  },

  computed: {
    isReadyToSubmit() {
      return !this.$v.$invalid;
    },
  },

  methods: {
    /**
     * Загрузка файла кейстора
     */
    onFileUpload(file) {
      if (file) {
        const reader = new window.FileReader();
        reader.onload = (e) => {
          this.keyStore = e.target.result;
        };
        reader.readAsText(file);
      }
    },

    /**
     * Функция импорта кошелька из кейстора
     */
    async importWallet() {
      try {
        const wallet = generateAddressesFromKeyStore(this.keyStore, this.password);
        if (wallet) {
          if (!this.$store.getters['wallets/hasPrivateKeyWallet'](wallet.address)) {
            await this.$store.dispatch('wallets/importWallet', {
              address: wallet.address,
              privateKey: wallet.privateKey,
              type: PRIVATE_KEY_TYPE,
            });

            // Отправка аналитики по импорту кошелька
            this.$ga.page({
              page: '/virtual/import',
              title: 'Wallet Was Imported',
              location: window.location.href,
            });
            this.goToTrading();
          } else {
            this.$store.dispatch('wallets/setActiveWallet', {
              address: wallet.address,
              type: PRIVATE_KEY_TYPE,
            });
            this.goToTrading();
          }
        }
      } catch (error) {
        this.error = error.message;
      }
    },

    goToTrading() {
      const activeTokenAddress = this.$store.getters['tokens/activeTokenContract'];
      if (activeTokenAddress) {
        this.$router.push({
          name: 'token',
          params: { token: activeTokenAddress },
        });
      } else {
        this.$router.push({ name: 'tokens' });
      }
    },
  },

  components: {
    ETextInputValidated,
    EButton,
    EUploadFiles,
  },

  mixins: [validationMixin, routesMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-wallet-import-keystore {
  &__container {
    margin-top: 20px;
    max-width: 625px;

    @include r(1200) {
      max-width: 439px;
    }
  }

  &__inputbox-password {
    position: relative;
    margin-bottom: 30px;
    margin-left: -50px;

    @include r(1200) {
      margin-bottom: 20px;
    }
  }

  &__upload {
    width: 250px;
    margin-bottom: 30px;
  }

  .e-upload-files {
    background-color: transparent;
  }

  .e-upload-files__upload {
    background: #fff;
  }

  &__error {
    margin-top: 10px;
    color: red;
  }
}
</style>




