<template>
  <div
    class="e-small-price-chart"
  >

    <div class="e-small-price-chart__chart-container">
      <svg
        class="e-small-price-chart__chart"
        :width="width"
        :height="height"
      >
        <g class="e-small-price-chart__chart-group"></g>
      </svg>
    </div>
  </div>
</template>

<script>
// Продакшин
import d3 from '@/libs/d3/d3.min';
// Девелопмент
// import d3 from './d3/d3';

export default {
  name: 'e-small-price-chart',

  props: {
    // Данные для отборажения
    dataSet: {
      type: Array,
      default: () => [],
    },

    chartType: {
      type: String,
      default: 'increase',
    },
  },

  data() {
    return {
      width: 10,
      height: 10,

      svg: null,
      defs: null,
      chart: null,

      strokeThikckness: 2,
    };
  },

  computed: {
    // Ковертация цены в число
    convertedDataSet() {
      const convertedDataSet = this.dataSet.map((item, index) => {
        const newItem = {};
        newItem.price = parseFloat(item.price);
        newItem.time = new Date(item.time);
        newItem.index = index;
        return newItem;
      });
      return convertedDataSet;
    },

    // Минимальное значение отображаемой цены
    min() {
      return d3.min(this.convertedDataSet, d => d.price) || 0;
    },

    // Максимальнео значение отображаемой цены
    max() {
      return d3.max(this.convertedDataSet, d => d.price) || 0;
    },

    delta() {
      return this.min === this.max
        ? this.min * 0.5
        : Math.min((this.max - this.min) * 0.1, this.min * 0.5);
    },

    xExtent() {
      return d3.extent(this.convertedDataSet, d => d.time);
    },

    xScale() {
      return d3.scaleTime()
        .domain(this.xExtent)
        .range([0, this.width]);
    },

    yScale() {
      return d3.scaleLinear()
        .domain([0, this.max])
        .range([this.height, 0]);
    },

    // Создание кривой
    line() {
      return d3.line()
        .x(d => d.x)
        .y(d => d.y)
        .curve(d3.curveLinear);
    },

    // define the area
    area() {
      return d3.area()
        .x(d => d.x)
        .y0(this.height)
        .y1(d => d.y)
        .curve(d3.curveLinear);
    },

    points() {
      return this.makeArrayOfPoints(this.convertedDataSet);
    },
  },

  watch: {
    // Отслеживание изменения данных
    convertedDataSet: function dataChange(newVal, oldVal) {
      this.updateData(newVal, oldVal);
    },
  },

  mounted() {
    this.$nextTick(() => {
      this.initialize();
      this.onResize();
    });
  },

  methods: {
    // Обработка изменения размера компонента
    onResize() {
      this.width = this.$el.offsetWidth;
      this.height = this.$el.offsetHeight;
      this.draw();
    },

    // Инициализация компонента
    initialize() {
      this.svg = d3.select(this.$el.querySelector('.e-small-price-chart__chart'))
        .attr('width', this.width)
        .attr('height', this.height);
      /* eslint-disable */
      // Создание градиента для fill графика
      this.defs = this.svg.append('defs');
      const linearGradIncrese = this.defs
        .append('linearGradient')
        .attr('id', `linear-gradient-increse-${this._uid}`);
      /* eslint-enable */

      linearGradIncrese
        .attr('x1', '0%')
        .attr('y1', '0%')
        .attr('x2', '0%')
        .attr('y2', '100%');

      // appending color to our gardient
      linearGradIncrese
        .append('stop')
        .attr('offset', '0%')
        .attr('stop-color', 'rgb(41, 232, 77)')
        .attr('stop-opacity', '0.37');
      linearGradIncrese
        .append('stop')
        .attr('offset', '100%')
        .attr('stop-color', 'rgb(41, 232, 77)')
        .attr('stop-opacity', '0');
      /* eslint-disable */
      const linearGradDecrese = this.defs
        .append('linearGradient')
        .attr('id', `linear-gradient-decrese-${this._uid}`);
      /* eslint-enable */
      linearGradDecrese
        .attr('x1', '0%')
        .attr('y1', '0%')
        .attr('x2', '0%')
        .attr('y2', '100%');
      // appending color to our gardient
      linearGradDecrese
        .append('stop')
        .attr('offset', '0%')
        .attr('stop-color', 'rgb(232, 41, 41)')
        .attr('stop-opacity', '0.37');
      linearGradDecrese
        .append('stop')
        .attr('offset', '100%')
        .attr('stop-color', 'rgb(231, 40, 40)')
        .attr('stop-opacity', '0');

      this.chart = d3.select(this.$el.querySelector('.e-small-price-chart__chart-group'));
    },

    draw() {
      this.drawCahrt();
    },

    drawCahrt() {
      const self = this;

      self.chart.selectAll('*').remove();

      const points = this.points;

      /* eslint-disable */
      // Add area
      self.chart.selectAll('.e-small-price-chart__chart-group .e-small-price-chart__area')
        .data([points], (d, i) => i)
        .enter()
        .append('path')
        .attr('class', 'e-small-price-chart__area')
        .attr('fill', this.chartType === 'increase' ? `url(#linear-gradient-increse-${this._uid})` : `url(#linear-gradient-decrese-${this._uid})`)
        .attr('d', self.area)
        .attr('class', 'e-small-price-chart__area');
      /* eslint-enable */

      // // Add line
      self.chart.selectAll('.e-small-price-chart__chart-group .e-small-price-chart__stroke')
        .data([points])
        .enter()
        .append('path')
        .attr('d', self.line)
        .attr('stroke-width', self.strokeThikckness)
        .attr('fill', 'none')
        .attr('stroke', this.chartType === 'increase' ? '#1FCE43' : '#DA4830')
        .attr('class', 'e-small-price-chart__stroke');
    },


    // Функция создания точек по массиву данных
    makeArrayOfPoints(dataSet) {
      const xExtent = d3.extent(dataSet, d => d.time);
      const xScale = d3.scaleTime()
        .domain(xExtent)
        .range([0, this.width]);

      const yScale = d3.scaleLinear()
        .domain([this.min - this.delta, this.max + this.delta])
        .range([this.height, 0]);

      const points = [];
      let i;
      const size = dataSet.length;

      for (i = 0; i < size; i += 1) {
        points.push({
          x: xScale(dataSet[i].time),
          y: yScale(dataSet[i].price),
        });
      }
      return points;
    },

    // Обновление данных
    updateData() {
      const self = this;

      const t = d3.transition()
        .duration(1000);

      const points = this.points;

      // Updating stroke
      self.svg.selectAll('.e-small-price-chart__stroke')
        .data([points])
        .transition(t)
        .attrTween('d', function updateStroke(d) {
          const previous = d3.select(this).attr('d');
          const current = self.line(d);
          return d3.interpolatePath(previous, current);
        });

      // Updating area
      self.svg.selectAll('.e-small-price-chart__area')
        .data([points], (d, i) => i)
        .transition(t)
        .attrTween('d', (d, i, el) => {
          const previous = d3.select(el[i]).attr('d');
          const current = self.area(d);
          // eslint-disable-next-line
          const excludeSegment = (a, b) => (a.x === b.x && a.x === self.width) || (a.x === b.x && a.x === 0);

          return d3.interpolatePath(previous, current, excludeSegment);
        });
    },
  },
};
</script>


<style lang="scss">
@import '~@/scss/styles';

.e-small-price-chart {
  width: 100%;
  height: 100%;
  overflow: hidden;

  // Секция с графиком
  &__chart-container {
    position: relative;
    width: 100%;
    height: 100%;
  }

  &__chart {
    position: absolute;
    top: 0;
    left: 0;
  }

  &__area {
    opacity: 0.6;
  }
}
</style>




