/**
 * @module store/prices/mutations
 *
 * @version 1.0.5
 */

import log from '@/util/logger';
import * as types from '@/store/mutation-types';
// import { addTradeToCandles } from './helpers';

const mutations = {
  /**
   * Начало асинхронной загрузки цен для текущего токена
   *
   * @since Version 1.0.3
   */
  [types.GET_CANDLES_ASYNC.PENDING](state) {
    state.candlesLoading = true;
    state.candles = [];
  },


  /**
   * Обработка ошибки при загрузке цен для текущего токенаа
   *
   * @since Version 1.0.1
   */
  [types.GET_CANDLES_ASYNC.FAILURE](state, error) {
    state.candlesLoading = false;
    state.pricesBuffer = [];
    log.error(`Error loading prices in candles: ${error}`, {
      module: 'prices',
      layer: 'backend',
    });
  },

  /**
   * Обработка успешной загрузки цен для текущего токена
   *
   * @since Version 1.0.1
   */
  [types.GET_CANDLES_ASYNC.SUCCESS](state, candles) {
    state.candlesLoading = false;
    state.candles = candles;
    // state.pricesBuffer.forEach((trade) => {
    //   state.candles = addTradeToCandles(state.candles, trade, state.interval * 1000);
    // });
    state.pricesBuffer = [];
  },

  /**
   * Mutation: Установка значения кендлов
   *
   * @since Version 1.0.4
   */
  [types.SET_CANDLES](state, candles) {
    state.candles = candles;
  },


  /**
   * Mutation: Установливает значение даты, начиная  с которой рисуется график
   *
   * @since Version 1.0.3
   */
  [types.SET_ACTIVE_PERIOD](state, activePeriod) {
    state.activePeriod = activePeriod;
  },


  /**
   * Mutation: Добавляет трейд в буфер
   *
   * @since Version 1.0.5
   */
  [types.PUSH_TO_PRICES_BUFFER](state, trade) {
    state.pricesBuffer.push(trade);
  },


  /**
   * Mutation: Устанавливает интервал
   */
  [types.SET_INTERVAL](state, interval) {
    state.interval = interval;
  },
};


export default mutations;



