<template>
  <div class="the-wallet-pages-import">
    <!-- BEGIN: Импорт кошельков -->
    <transition-content>
      <template v-if="currentVisibleBlock === 0">
        <e-bottom-layout :borderTop="true">
          <template slot="body">
            <div class="the-wallet-pages-import__heading">
              <e-heading
                title="Create a new wallet"
                subtitle="Create a new crypto wallet in 1 click"
                :icon="walletHeadingIcon"
                :btns="true"
              >
                <e-button
                  fill="green"
                  @click="handleGenerate"
                  padding="wide"
                  iconMargin="md"
                  data-test="generateWallet"
                >
                  Create new wallet
                </e-button>
              </e-heading>
            </div>

            <div class="the-wallet-pages-import__main-heading the-wallet-pages-import__heading">
              <e-heading
                title="Import your current wallet"
                subtitle="Log in using one of this options:"
                :icon="importWalletHeadingIcon"
              >
              </e-heading>
            </div>

            <!-- BEGIN: Tabs to chose import method -->
            <div class="the-wallet-pages-import__tabs">
              <div
                class="the-wallet-pages-import__tab"
                :class="{
                  'is-active': i === activeTab
                }"
                v-for="(item, i) in tabs"
                @click="activeTab = i"
                :key="i"
              >
                <div class="the-wallet-pages-import__tab-icon">
                  <e-icon :src="item.icon"/>
                </div>
                {{item.title}}
              </div>
            </div>
            <!-- END: Tabs to chose import method -->

            <!-- BEGIN: Private Key -->
            <template v-if="activeTab === 0">
              <div class="the-wallet-pages-import__form">
                <the-wallet-signup/>
              </div>
            </template>
            <!-- END: Private Key -->

            <!-- BEGIN: Keystore -->
            <template v-if="activeTab === 1">
              <div class="the-wallet-pages-import__form">
                <the-wallet-import-keystore/>
              </div>
            </template>
            <!-- END: Keystore -->

            <!-- BEGIN: Mnemonic Phrase -->
            <template v-if="activeTab === 2">
              <div class="the-wallet-pages-import__form">
                <the-wallet-import-mnemonic-phrase/>
              </div>
            </template>
            <!-- END: Mnemonic Phrase -->

            <div class="the-wallet-pages-import__footer">
              <div class="the-wallet-pages-import__footer-login">
                <e-icon :src="iconMetamask"/>
                Log In with &nbsp;
                <div
                  class="the-wallet-pages-import__link"
                  :class="{ 'the-wallet-pages-import__link_pending': pendingWalletConnection }"
                  @click.prevent="loginWithMetamask"
                >
                  Metamask
                </div>
              </div>
              <div class="the-wallet-pages-import__footer-login">
                <e-icon :src="iconNanoLedgerS"/>
                or &nbsp;
                <a
                  class="the-wallet-pages-import__link"
                  :class="{ 'the-wallet-pages-import__link_pending': pendingWalletConnection }"
                  @click.prevent="loginWithLedger"
                >
                  Ledger Nano S
                </a>
              </div>
            </div>
          </template>
          <div class="the-wallet-pages-import__footer-boorder"></div>
        </e-bottom-layout>
      </template>
    </transition-content>
    <!-- END: Импорт кошельков -->

    <!-- BEGIN: Инструкция по подлючению Метамаск -->
    <transition-content>
      <div
        v-if="currentVisibleBlock !== 0"
        class="the-wallet-pages-import__wrapper"
      >
        <div class="the-wallet-pages-import__header">
          <e-button-back :icon="signUpOptions[signUpOptionIndex].icon" @buttonOnClick="returnToMain" />
          <e-heading
            :title="signUpOptions[signUpOptionIndex].instructionTitle"
            :subtitle="signUpOptions[signUpOptionIndex].text"
            :fullWidth="true"
          />
        </div>

        <div class="the-wallet-pages-import__instruction">
          <div class="the-wallet-pages-import__instruction-wrapp">
            <transition-content-relative>
              <e-instruction
                :key="signUpOptionIndex"
                :instructionData="instractionsText"
              />
            </transition-content-relative>
          </div>
          <e-items-title
            title="see also"
            :icon="iconCheckSquared"
            iconColor="green"
            :counterInTitle="false"
          />
          <the-signup-options
            :signUpOptions="seeAlso"
            :hide-btn="1"
          />
        </div>
      </div>
    </transition-content>
    <!-- END: Инструкция по подлючению Метамаск -->
  </div>
</template>

<script>
/**
 * Component: Страница импорта кошелька
 *
 * @version 1.0.2
 */

import { mapGetters, mapActions } from 'vuex';

import { POPUP_WALLET } from '@/store/constants';
import { routesMixin } from '@/util/mixins';

// Components
import EBottomLayout from '@/components/EBottomLayout';
import EButton from '@/components/EButton';
import EButtonBack from '@/components/EButtonBack';
import EHeading from '@/components/EHeading';
import EIcon from '@/components/EIcon';
import EInstruction from '@/components/EInstruction';
import EItemsTitle from '@/components/EItemsTitle';
import TheSignupOptions from '@/containers/blocks/TheSignupOptions';
import TheWalletImportKeystore from '@/containers/blocks/TheWalletImportKeystore';
import TheWalletImportMnemonicPhrase from '@/containers/blocks/TheWalletImportMnemonicPhrase';
import TheWalletSignup from '@/containers/blocks/TheWalletSignup';

// Icons
import iconCheckSquared from '@/icons/e-icon_checkmark-squared.svg';
import iconKeystore from '@/icons/e-icon_keystore.svg';
import iconLedger from '@/icons/e-icon_ledger.svg';
import iconMetamask from '@/icons/e-icon_metamask-normal.svg';
import iconMnemonic from '@/icons/e-icon_mnemonic.svg';
import iconNanoLedgerS from '@/icons/e-icon_nano-ledger-s.svg';
import iconRoundedArrow from '@/icons/e-icon_rounded_arrow.svg';
import iconWallet from '@/icons/e-icon_wallet.svg';
import iconWalletAddress from '@/icons/e-icon__wallet-address-key.svg';
import importWalletHeadingIcon from '@/icons/e-icon_import_wallet.svg';
import walletHeadingIcon from '@/icons/e-icon_plus_wallet4.svg';

// Transitions
import TransitionContent from '@/containers/transitions/TransitionContent';
import TransitionContentRelative from '@/containers/transitions/TransitionContentRelative';

export default {
  name: 'the-wallet-pages-import',

  data() {
    return {
      /**
       * Текущий отображаемый блок
       * 0 - Импорт кошельков
       * 1 - Инструкция по подключению метамаска
       * 2 - Инструкция по подключению Ledger
       */
      currentVisibleBlock: 0,

      activeTab: 0,
      tabs: [
        {
          title: 'Address and Private Key',
          icon: iconWalletAddress,
        },
        {
          title: 'Keystore',
          icon: iconKeystore,
        },
        {
          title: 'Mnemonic Phrase',
          icon: iconMnemonic,
        },
      ],
      signUpOptionIndex: -1,
      signUpOptions: [
        {
          title: 'MetaMask',
          icon: iconMetamask,
          iconWidth: 'wide',
          text: 'Google Chrome Plugin',
          instructionTitle: 'MetaMask',
          btns: [
            {
              label: 'website',
              handler: () => {
                const win = window.open('https://metamask.io/', '_blank');
                if (win) {
                  win.focus();
                }
              },
            },
            {
              label: 'instructions',
              handler: () => {
                this.currentVisibleBlock = 2;
                this.signUpOptionIndex = 0;
              },
            },
          ],
        },
        {
          title: 'Ledger Nano S',
          icon: iconLedger,
          iconFill: 'blue',
          text: 'Cryptocurrency hardware wallet',
          instructionTitle: 'Ledger Nano S',
          btns: [
            {
              label: 'website',
              handler: () => {
                const win = window.open('https://www.ledgerwallet.com/products/ledger-nano-s', '_blank');
                if (win) {
                  win.focus();
                }
              },
            },
            {
              label: 'instructions',
              handler: () => {
                this.currentVisibleBlock = 2;
                this.signUpOptionIndex = 1;
              },
            },
          ],
        },
        {
          title: 'Import Wallet',
          icon: iconWallet,
          iconFill: 'green',
          text: 'Wallet Address / Private Key',
          instructionTitle: 'How to Import a Wallet',
          btns: [
            {
              label: 'import',
              handler: () => {
                this.importWallet();
              },
            },
            {
              label: 'instructions',
              handler: () => {
                this.currentVisibleBlock = 2;
                this.signUpOptionIndex = 2;
              },
            },
          ],
        },
      ],

      metamaskLoading: false,
      ledgerLoading: false,

      // Icons
      importWalletHeadingIcon,
      walletHeadingIcon,
      iconRoundedArrow,
      iconMetamask,
      iconLedger,
      iconNanoLedgerS,
      iconCheckSquared,
    };
  },

  computed: {
    ...mapGetters({
      allWallets: 'wallets/allWallets',
    }),

    pendingWalletConnection() {
      return this.metamaskLoading || this.ledgerLoading;
    },

    seeAlso() {
      return this.signUpOptions.filter((item, index) => {
        if (index === this.signUpOptionIndex) return false;
        return item;
      });
    },

    instractionsText() {
      switch (this.signUpOptionIndex) {
        case 0:
          return {
            textual: [
              'To start using your MetaMask wallet, you need first to install the <a class="e-instruction__link" target="_blank" href="https://chrome.google.com/webstore/detail/nkbihfbeogaeaoehlefnkodbefgpgknn">MetaMask Google Chrome extension</a> and unlock the wallet.',
              'Once you have unlocked the wallet, your wallet address will automatically appear in the accounts dropdown in the top right corner of the website.',
              'When you are interacting with the exchange by placing orders, depositing or withdrawing funds, you will be asked to confirm this actions in your MetaMask wallet (a popup will appear).',
              'To deposit funds to the exchange go to "My Wallet", select the token you wish to deposit and press the deposit button.',
            ],
          };
        case 1:
          return {
            textual: [
              'To start using EtherMium with your hardware wallet, connect your Ledger Nano S to your computer, Unlock your wallet and open the Ethereum App.',
              'If you are using an older version of firmware on your Ledger, make sure that Browser Support is enabled.',
              'Once you connect and unlock your Ledger, your wallet address should automatically appear in the "Accounts" menu at the top right corner of the website.',
            ],
          };
        case 2:
          return {
            textual: [
              'To import your Ethereum wallet into EtherMium, choose the Import Wallet option when you connect.',
              'Select your preferred private key format and enter the secret string. Then press "Import wallet" and you are good to go.',
              'When your wallet is successfully imported, your wallet address will appear in the "Accounts" menu at the top right corner of the website.',
            ],
          };
        default:
          throw Error(`wrong signUpOptionIndex: ${this.signUpOptionIndex}`);
      }
    },
  },

  methods: {
    ...mapActions({
      setActiveWallet: 'wallets/setActiveWallet',
    }),

    /**
     * Генерация нового кошелька
     */
    handleGenerate() {
      this.$router.push(this.$popupRoute(POPUP_WALLET));
    },

    /**
     * Логин с помощью метамаска
     * Если есть метамасковский кошелек, то он делается активным, если нет выводится инструкция, как подключить метамаск
     */
    async loginWithMetamask() {
      if (this.metamaskLoading) return;
      this.metamaskLoading = true;
      await this.$store.dispatch('wallets/loadMetamaskWallets');
      const wallet = this.$store.state.wallets.providedWallets[0];
      if (wallet) {
        // Делаем активным найденный кошелек Метамаск
        this.setActiveWallet(wallet);
      } else {
        // Открытие инструкции по подключению Метамаска
        this.signUpOptionIndex = 0;
        this.currentVisibleBlock = 1;
      }
      this.metamaskLoading = false;
    },

    /**
     * Логин с помощью Ledger
     * Если есть Ledger кошелек, то он делается активным, если нет выводится инструкция, как подключить Ledger
     */
    async loginWithLedger() {
      if (this.ledgerLoading) return;
      this.ledgerLoading = true;
      await this.$store.dispatch('wallets/loadLedgerWallets');
      const wallet = this.$store.state.wallets.ledgerWallets[0];
      if (wallet) {
        // Делаем активным найденный кошелек Ledger
        this.setActiveWallet(wallet);
      } else {
        // Открытие инструкции по подключению Ledger
        this.signUpOptionIndex = 1;
        this.currentVisibleBlock = 2;
      }
      this.ledgerLoading = false;
    },

    returnToMain() {
      this.currentVisibleBlock = 0;
    },
  },

  components: {
    EBottomLayout,
    EButton,
    EButtonBack,
    EHeading,
    EIcon,
    EInstruction,
    EItemsTitle,
    TheSignupOptions,
    TheWalletImportKeystore,
    TheWalletImportMnemonicPhrase,
    TheWalletSignup,
    TransitionContent,
    TransitionContentRelative,
  },

  mixins: [routesMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-wallet-pages-import {
  $this: &;

  /*position: relative;*/
  /*height: 100%;*/
  /*margin-bottom: 46px;*/


  @include r(1200) {
    padding-bottom: 0;
  }

  &__link {
    cursor: pointer;
    color: #0000F8;
    text-decoration: none;
    border-bottom: 1px solid rgba(1, 35, 231, .4);
    transition: color .15s ease-in-out, border-color .15s ease-in-out;

    &:hover {
      color: #000;
      border-color: #000;
    }

    &_pending {
      cursor: progress;
    }
  }

  &__tabs {
    display: flex;
    padding-left: 195px;

    @include r(1200) {
      padding-left: 105px;
    }
  }

  &__tab {
    color: #0123E7;
    cursor: pointer;
    font-size: 18px;
    font-weight: 600;

    @include r(1200) {
      font-size: 16px;
    }

    &.is-active {
      color: #000000;
    }

    &:nth-child(1) {
      &.is-active {
        #{$this}__tab-icon {
          svg g {
            path:nth-child(1) {
              fill: #AFAFAF;
            }
            path:nth-child(2),
            path:nth-child(4) {
              fill: #000000;
            }
          }
        }
      }
    }

    &:nth-child(2) {
      &.is-active {
        #{$this}__tab-icon {
          svg g {
            path:nth-child(1),
            path:nth-child(2) {
              fill: #AFAFAF;
            }
            path:nth-child(3) {
              fill: #000000;
            }
          }
        }
      }
    }

    &:nth-child(3) {
      &.is-active {
        #{$this}__tab-icon {
          svg g {
            path:nth-child(1) {
              fill: #AFAFAF;
            }
            path:nth-child(3) {
              fill: #000000;
            }
          }
        }
      }
    }

    &:hover {
      color: #000000;
    }

    &:not(:last-child):after {
      content: "/";
      font-size: 18px;
      font-weight: 600;
      color: #E9F1F9;
      padding-left: 18px;
      padding-right: 18px;
    }
  }

  &__tab-icon {
    position: relative;
    display: inline-block;
    width: 20px;
    height: 20px;
    top: 5px;
    padding-right: 3px;
    fill: #647AFD;

    .e-icon_wallet-adress_darked {
      fill: $color-blue;
    }
  }

  &__form {
    padding: 21px 0 68px 194px;

    @include r(1200) {
      padding: 16px 0 68px 104px;
    }
  }

  &__heading,
  &__main-heading {
    height: 120px;
    margin-bottom: 50px;

    @include r(1200) {
      height: 80px;
    }

    .e-heading__title {
      color: #000000;
      font-size: 26px;
      font-weight: 600;
    }

    .e-heading__subtitle {
      color: #8383AC;
      font-size: 17px;
      font-weight: 600;
      margin-top: 12px;

      @include r(1200) {
        font-size: 16px;
      }
    }

    .e-heading__textual {
      padding-top: 18px;
      padding-left: 30px;

      @include r(1200) {
        padding-top: 1px;
        padding-left: 14px;
      }
    }

    .e-heading__btns {
      padding-top: 12px;

      .e-button_padding_wide {
        padding-left: 30px;
        padding-right: 30px;
      }
    }
  }

  &__main-heading {
    margin-bottom: 0;
  }

  &__heading-private-key {
    margin-bottom: -30px;
  }

  &__footer-boorder {
    width: 100%;
    height: 1px;
    background-color: #8281AF;
  }

  &__footer {
    border-top: 1px solid #E9F1F9;
    padding-top: 20px;
    display: flex;
    justify-content: center;
    color: #000000;
    font-weight: 600;
    font-size: 16px;
    justify-content: center;
  }

  &__footer-login {
    padding-top: 20px;
    display: flex;
    align-items: center;

    .e-icon {
      width: 21px;
      height: 21px;
      margin-right: 10px;
      line-height: 0;
    }

    &:not(:last-child):after {
      content: "/";
      font-size: 18px;
      font-weight: 600;
      color: #E9F1F9;
      padding-left: 30px;
      padding-right: 30px;
    }
  }

  &__header {
    display: flex;
    flex-direction: row;
  }

  &__instruction {
    border-top: 2px solid #F8F9FC;
    margin-top: 38px;
    padding-top: 35px;
  }

  &__instruction-wrapp {
    padding-left: 138px;
    margin-bottom: 40px;
  }
}
</style>




