<template>
  <div class="e-modal-trades">
    <div class="e-modal-trades__header">
      <span v-if="!customHeader">{{ headerTitle }}</span>
      <slot
        v-else
        name="header"
      />
    </div>
    <div class="e-modal-trades__body">
      <slot name="body"/>
    </div>
    <div class="e-modal-trades__footer">
      <button
        v-if="btnCancel"
        class="e-modal-trades__btn"
        @click="close"
      >
        {{btnText}}
      </button>
      <slot
        v-else
        name="footer"
      />
    </div>
  </div>
</template>

<script>
export default {
  name: 'e-modal-trades',

  props: {
    customHeader: {
      type: Boolean,
      default: false,
    },

    headerTitle: {
      type: String,
      default: '',
    },

    btnCancel: {
      type: Boolean,
      default: true,
    },

    btnText: {
      type: String,
      default: 'Cancel',
    },
  },

  methods: {
    close() {
      this.$emit('close');
    },
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-modal-trades {
  background-color: $color-light-blue;

  &__header {
    background-color: $color-white;
    box-shadow: 0 0 3px 0 rgba(0,0,0,0.2);
    color: $color-dark-blue;
    font-size: 16px;
    font-weight: 800;
    line-height: 1.375;
    padding: 25px 10px 21px;
  }

  &__body {
    padding: 30px;
  }

  &__btn {
    background-color: $color-grey-light;
    color: $color-blue;
    font-size: 12px;
    font-weight: 700;
    display: block;
    height: 50px;
    letter-spacing: 1.09px;
    text-transform: uppercase;
    width: 100%;
  }
}
</style>



