<template>
  <div class="e-button-back">
    <e-button
      v-if="withBackArrow"
      type="circle"
      fill="white-circle"
      size="circle"
      padding="none"
      iconMargin="auto"
      adaptiveMod="circle-arrow"
      :icon="iconArrowLeft"
      @click="handleClick"
    >
    </e-button>
    <e-circle-icon
      :icon="icon"
      :containerShift="true"
      :background="background"
      :biggerOnTablet="true"
    />
  </div>
</template>

<script>
import iconArrowLeft from '@/icons/e-icon_arrow_left.svg';

import EButton from '@/components/EButton';
import ECircleIcon from '@/components/ECircleIcon';

export default {
  name: 'e-button-back',

  data() {
    return {
      iconArrowLeft,
    };
  },

  props: {

    icon: {
      type: String,
      default: '',
    },

    background: {
      type: String,
      default: 'white',
    },

    withBackArrow: {
      type: Boolean,
      default: true,
    },
  },

  components: {
    EButton,
    ECircleIcon,
  },

  methods: {
    handleClick() {
      this.$emit('buttonOnClick');
    },
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-button-back {
  display: flex;
}
</style>



