import getNameForNamelessToken from '@/util/getNameForNamelessToken';
import backend from './endpoint';

const mapSearchTokens = result => result.data.map(t => ({
  contract: t.token_address,
  name: t.name || getNameForNamelessToken(t.token_address),
  shortName: t.symbol || getNameForNamelessToken(t.token_address),
  websiteUrl: t.website_url,
  isWebsiteVerified: Boolean(t.is_website_verified),
}));

// eslint-disable-next-line import/prefer-default-export
export const searchTokens = async (term) => {
  const result = await backend.http.get('/api/tokens/external-search', {
    params: { term },
  });
  return mapSearchTokens(result.data);
};



