/**
 * @module store/contracts/actions
 *
 * @version 1.1.0
 */


import * as types from '@/store/mutation-types';
import { getContracts, contractSubscribe as contractSubscribeRequest } from '@/api/backend';
import { ACTIVE_MAIN_CONTRACT_KEY } from '@/store/constants';
import log from '@/util/logger';
import { getStorageValue, setStorageValue } from '@/util/storage';
import { wrapCachedAction } from '@/store/utils';

const loadExchangeContracts = async ({ commit, rootGetters }) => {
  commit(types.GET_EXCHANGE_CONTRACTS_ASYNC.PENDING);
  let contracts;
  try {
    contracts = await getContracts();
  } catch (error) {
    commit(types.GET_EXCHANGE_CONTRACTS_ASYNC.FAILURE);
    if (rootGetters.isConnected) {
      log.error(`Error loading exchange contracts: ${error}`, {
        module: 'contracts',
        layer: 'backend',
      });
    }
  }
  if (contracts) {
    commit(types.GET_EXCHANGE_CONTRACTS_ASYNC.SUCCESS, contracts);
  }
};

/**
 * Загружает всех контрактов с бекенда
 *
 * @see See [Docs](https://docs.ethermium.devify.net/#api-Contracts-WsContractsGetAll)
 * @since Version 1.0.1
 */
const loadContracts = wrapCachedAction('loadContracts', async ({ state, commit, dispatch, rootGetters }) => {
  commit(types.GET_CONTRACTS_ASYNC.PENDING);
  await dispatch('loadExchangeContracts');
  if (state.exchangeContractsFailure) {
    commit(types.GET_CONTRACTS_ASYNC.FAILURE);
  } else {
    const blockchainApi = rootGetters['blockchain/contractsApi'];
    let affiliate;
    try {
      affiliate = await blockchainApi.affiliateContractAddress();
    } catch (error) {
      commit(types.GET_CONTRACTS_ASYNC.FAILURE);
      if (rootGetters.isConnected) {
        log.error(`Error: affiliate contract load: ${error}`, {
          module: 'contracts',
          layer: 'blockchain',
        });
      }
    }
    if (affiliate) {
      commit(types.GET_CONTRACTS_ASYNC.SUCCESS, { affiliate });
    }
  }
});

/**
 * Action: Загружает активный главный контракт из локалсторенда или если там его нет с бекенда
 *
 * @since Version 1.0.2
 */
const loadActiveExchangeContract = wrapCachedAction('loadActiveExchangeContract', async ({ state, commit, dispatch }) => {
  commit(types.GET_ACTIVE_CONTRACT.PENDING);
  const storedContract = getStorageValue(ACTIVE_MAIN_CONTRACT_KEY);
  if (storedContract !== null) {
    // Если в локал сторедже есть сохраненный текущий главный контракт
    commit(types.GET_ACTIVE_CONTRACT.SUCCESS, storedContract);
  } else {
    await dispatch('loadExchangeContracts');
    if (state.exchangeContractsFailure) {
      commit(types.GET_ACTIVE_CONTRACT.FAILURE);
    } else {
      commit(types.GET_ACTIVE_CONTRACT.SUCCESS, state.exchangeContracts[0]); // latest is first
      setStorageValue(ACTIVE_MAIN_CONTRACT_KEY, state.activeMainContract);
    }
  }
});

/**
 * Action: Изменение активного главного контракта. Сохраняем главный контракт в localstorage
 *
 * @since Version 1.0.2
 */
const changeActiveMainContract = async ({ commit, state }, contract) => {
  if (state.activeMainContract === null || contract.version !== state.activeMainContract.version) {
    commit(types.SET_ACTIVE_MAIN_CONTRACT, contract);
    setStorageValue(ACTIVE_MAIN_CONTRACT_KEY, state.activeMainContract);
  }
};

/**
 * Action: Подписка на конкретный контракт
 * (только после нее можно получать ивенты обновления)
 *
 * Оптимизация:
 * Promise разрешается как только запрос на подписку отправлен, не дожидаясь того чтобы тот вернулся обратно.
 *
 * @since Version 1.1.0
 */
const subscribeToContract = wrapCachedAction('subscribeToContract', async ({ state, commit, dispatch }) => {
  commit(types.SUBSCRIBE_TO_CONTRACT_ASYNC.PENDING);
  await dispatch('loadActiveExchangeContract');
  // Оптимизация: Promise resolve-иться как только запрос на подписку отправлен, не дожидаясь ответа.
  (async () => {
    if (!state.activeMainContract) {
      commit(types.SUBSCRIBE_TO_CONTRACT_ASYNC.FAILURE);
    } else {
      await contractSubscribeRequest(state.activeMainContract.contractAddress);
      commit(types.SUBSCRIBE_TO_CONTRACT_ASYNC.SUCCESS);
    }
  })();
});

export default {
  loadExchangeContracts,
  loadContracts,
  loadActiveExchangeContract,
  changeActiveMainContract,
  subscribeToContract,
};



