<template>
  <div
    v-if="displayed"
    class="e-modal"
    :class="{
      'e-modal_secondary': secondaryDesign,
      'e-modal_auto-height': autoHeight,
      'e-modal_dark' : darkBackground,
    }"
  >
    <slot name="logo"/>
    <div class="e-modal__body">
      <div class="e-modal__inner">
        <slot name="inner"/>
      </div>
      <div class="e-modal__close" @click="close" v-if="btnClose"><e-icon :src="iconClose"/></div>
    </div>
    <div
      v-if="!noBackground"
      class="e-modal__cover"
      @click="close"
    />
  </div>
</template>

<script>
import iconClose from '@/icons/e-icon_close.svg';

import EIcon from '@/components/EIcon';

export default {
  name: 'e-modal',

  props: {
    level: {
      type: Number,
      default: 0,
    },

    secondaryDesign: {
      type: Boolean,
      default: false,
    },

    autoHeight: {
      type: Boolean,
      default: false,
    },
    btnClose: {
      type: Boolean,
      default: true,
    },

    darkBackground: {
      type: Boolean,
      default: false,
    },

    noBackground: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    displayed() {
      return this.level === this.$store.getters['ui/topModalLevel'];
    },
  },

  created() {
    Object.assign(this, { iconClose });
    this.$store.commit('ui/SHOW_MODAL', this.level);
  },

  beforeDestroy() {
    this.$store.commit('ui/HIDE_MODAL', this.level);
  },

  methods: {
    close() {
      this.$emit('close');
    },
  },

  components: { EIcon },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-modal {
  $this: &;

  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1001;
  display: flex;
  justify-content: center;
  align-items: center;

  &_secondary {
    #{$this}__body {
      width: 369px;
    }

    #{$this}__close {
      top: -5px;
      right: -5px;
      height: 10px;
      width: 10px;

      .e-icon svg {
        fill: #fff;
      }

      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background: #545575;
        border-radius: 50%;
        transform: scale(3);
      }
    }
  }

  &_auto-height {
    #{$this}__body {
      min-height: auto;
    }
  }

  &_dark {
    #{$this}__cover {
      background-color: rgba(#595959, 0.76);
    }
  }

  &__body {
    position: relative;
    width: 390px;
    min-height: 360px;
    box-sizing: border-box;
    z-index: 1;
    border-radius: 10px;
    text-align: center;
    box-shadow: 0 3px 10px rgba(#94A5B4, .14);
  }

  &__inner {
    background-color: #FFF;
    border-radius: 3px;
    overflow: hidden;
  }

  &__cover {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(#0D105B, .46);
  }

  &__close {
    position: absolute;
    top: 19px;
    right: 18px;
    width: 12px;
    height: 16px;
    cursor: pointer;

    .e-icon svg {
      fill: #8889AE;
    }
  }
}
</style>



