/**
 * @module store/prices/actions
 *
 * @version 1.0.4
 */

import * as types from '@/store/mutation-types';
import log from '@/util/logger';
import { ACTION_INTERNAL_TRADE, ACTION_TRADE_CONFIRMATION } from '@/api/backend';
import { addTradeToCandles } from './helpers';

/**
 * Action: Загрузка всех цен в виде свеце по всем трейдам
 * Should be called only after activeToken is set
 *
 * @since Version 1.0.2
 */
const loadCandles = async ({ state, rootGetters, commit, dispatch }) => {
  if (!state.candlesLoading) {
    commit(types.GET_CANDLES_ASYNC.PENDING);
    await Promise.all([
      dispatch('contracts/subscribeToContract', undefined, { root: true }), // ensures active contract loaded
      dispatch('tokens/subscribeToToken', undefined, { root: true }),
    ]);

    if (!rootGetters['tokens/activeToken']) {
      commit(types.GET_CANDLES_ASYNC.FAILURE);
      return;
    }
    const contractAddress = rootGetters['contracts/activeMainContractAddress'];
    if (!contractAddress) {
      commit(types.GET_CANDLES_ASYNC.FAILURE);
      log.error('contractAddress must not be null', {
        module: 'prices',
        layer: 'store',
      });
    }
  }
};

/**
 * Action: Synchronous Добавляет трейд на график
 *
 * @since Version 1.0.3
 */
const pushPrice = ({ state, commit }, { action, isConfirmed, ...trade }) => {
  if (action === ACTION_INTERNAL_TRADE || (action === ACTION_TRADE_CONFIRMATION && isConfirmed === null)) {
    // Добавляем новый трейд на график
    const newCandles = addTradeToCandles(state.candles, trade, state.interval * 1000);
    commit('SET_CANDLES', newCandles);
  } else if (isConfirmed === false) {
    // Трейд отменен, удаляем его с графика
    // TODO: Нужно реализовать оптимальный альгоритм удаления трейдов с графика, сложность состоит в том, что мы добавляем итеративно трейды, по одному, и по большому счету храним в памяти только уже массив готовых
  }
};

export default {
  loadCandles,
  pushPrice,
};



