/**
 * Backend API
 *
 * @module api/backend
 * @version 1.1.4
 */

import { ETH_TOKEN } from '@/util/constants';
import throttle from 'lodash/throttle';
import flow from 'lodash/flow';
import { BigNumber } from 'bignumber.js';
import toDisplayedPrice from '@/util/format/toDisplayedPrice';
import { HISTORY_PAGE_SIZE, PAGE_SIZE_TRADES, THROTTLE_DELAY_VOLUMES, HISTORY_RECENT_SIZE } from '@/config';
import backend from './endpoint';
import { mapContract, mapVolumes, mapOperation, mapNews, mapGasPrice } from './helpers';
import { CONTRACT_TYPE_EXCHANGE } from './constants';

window.backend = backend; // test global var

export const reconnect = () => {
  if (backend.socket.disconnected) {
    backend.socket.open();
  }
};

export const close = () => {
  const { socket } = backend;
  socket.close();
};

/**
 * App configs. Contain some fee-related info, limits and timestamp
 *
 * @todo refactor to return camelCased values
 * @returns {Promise<{trade_confirmations: number, fund_confirmations: number, trade_taker_gas_limit_fee: number, withdraw_gas_limit_fee: number, order_min_eth: number, server_timestamp: number, gasPriceType: string}>}
 */
export const getConfigs = async () => {
  const config = await backend.wsRequest('/platform/configs');
  return {
    gasPriceType: config.ow_gas_price_type,
    ...config,
  };
};

/**
 * Server timestamp. Can be used to create signature nonce.
 *
 * @returns {Promise<number>}
 */
export const getTimestamp = async () => {
  const response = await backend.wsRequest('/platform/configs');
  return response.server_timestamp;
};

/**
 * Total number of wallets
 *
 * @see See [Docs](https://docs.ethermium.devify.net/#api-Platform-WsPlatformCountWallets)
 * @returns {Promise<number>}
 */
export const totalWallets = () => backend.wsRequest('/platform/count-wallets');

/**
 * Задает функцию колбек, которая будет вызываться при разрыве соединении с сервером
 *
 * @param {Function} callback - Колбек, который будет вызываться при разрыве соединения с сервером
 * @returns {void}
 */
export const onDisconnect = (callback) => {
  backend.socket.on('disconnect', callback);
};

/**
 * Задает функцию колбек, которая будет вызываться при соединении или востановлении соединения с сервером
 *
 * @param {Function} callback - Колбек, который будет вызываться при конекте
 * @returns {void}
 */
export const onConnect = (callback) => {
  backend.socket.on('connect', callback);
};

export const onConnectError = (callback) => {
  backend.socket.on('connect_error', callback);
};

/**
 * Задает функцию колбек, которая будет вызываться при востановлении соединения с сервером
 *
 * @param {Function} callback - Колбек, который будет вызываться при востановлении соединения с сервером
 * @returns {void}
 */
export const onReconnect = (callback) => {
  backend.socket.on('reconnect', callback);
};

export const removeReconnectListener = (listener) => {
  if (listener != null) {
    backend.socket.removeListener('reconnect', listener);
  }
};

// volumes

export const getVolumes = () => backend.wsRequest('/trades/volumes').then(mapVolumes);

export const removeTradeVolumesListener = () => {
  backend.socket.removeAllListeners('trades_volumes');
};

export const onTradeVolumesChange = (callback) => {
  removeTradeVolumesListener();
  backend.socket.on('trades_volumes', throttle(flow([mapVolumes, callback]), THROTTLE_DELAY_VOLUMES, { leading: false }));
};


// Gas price

/**
 * Fetches Gas Price from backend.
 *
 * @param {{ gasPriceType: string }} params
 * @returns {Promise<{fast: BigNumber|null}>}
 */
export const getGasPrice = async (params) => {
  const event = await backend.wsRequest('/eth/gas-price');
  return mapGasPrice(event, params);
};

export const removeGasPriceListener = () => {
  backend.socket.removeAllListeners('gas_price');
};

/**
 * React to gas price change
 *
 * @param handler {function}
 * @param getParams {function} returns always resolved Promise
 */
export const onGasPrice = ({ handler, getParams }) => {
  removeGasPriceListener();
  backend.socket.on('gas_price', async event => (
    handler(mapGasPrice(event, await getParams()))
  ));
};

// Funds
/**
 *
 * @param userAddress {string}
 * @param contractAddress {string}
 * @returns {Promise<{data: object}>}
 */
export const getPendingBalanceOperations = async ({ userAddress, contractAddress }) => {
  const { data } = await backend.wsRequest('/funds/get', {
    filters: {
      user_address: userAddress,
      contract_address: contractAddress,
      is_pending: true,
    },
    limit: 1000,
  });
  return {
    data: data.map(mapOperation),
  };
};

// Funds
