/**
 * Action: Удаляет активный кошелек из локального стора
 */
const forgetPrivateWallet = ({ state, commit, dispatch }, address) => {
  commit(types.FORGET_WALLET, address);
  storePrivateKeyWallets(state.localWallets);
  dispatch('selectWalletIfAbsent');
};

const loadTotalWallets = async ({ rootGetters, commit }) => {
  try {
    const totalWallets = await getTotalWallets();
    commit(types.SET_TOTAL_WALLETS, totalWallets);
  } catch (e) {
    if (rootGetters.isConnected) {
      log.error(e.message);
    }
  }
};

// helper actions

/**
 * Action: Synchronous: selects wallet if active wallet doesn't exist.
 * Intended to be used in other actions.
 */
const selectWalletIfAbsent = ({ state, getters, dispatch }) => {
  if (!getters.activeWallet) {
    if (state.ledgerWallets.length) {
      dispatch('setActiveWallet', state.ledgerWallets[0]);
    } else if (state.providedWallets.length) {
      dispatch('setActiveWallet', state.providedWallets[0]);
    } else if (state.localWallets.length) {
      dispatch('setActiveWallet', state.localWallets[0]);
    } else {
      window.localStorage.removeItem(ACTIVE_WALLET_LOCAL_STORAGE_KEY);
    }
  }
};

const subscribeToAccount = wrapCachedAction('subscribeToAccount', async ({ getters, rootGetters }) => {
  // Оптимизация: Promise resolve-иться как только запрос на подписку отправлен, не дожидаясь ответа.
  (async () => {
    try {
      await sendAccountSubscription(getters.activeWalletAddress);
    } catch (error) {
      if (rootGetters.isConnected) {
        log.error(`Error subscribing to account: ${error}`, {
          module: 'wallets',
          layer: 'backend',
        });
      }
    }
  })();
});

export default {
  selectWalletIfAbsent,
  createWallet,
  importWallet,
  loadTotalWallets,
  loadPrivateWallets,
  loadMetamaskWallets,
  unloadMetamaskWallets,
  loadLedgerWallets,
  forgetPrivateWallet,
  setActiveWallet,
  subscribeToAccount,
};



