import { MAIN_NETWORK_ID, TEST_NETWORK_ID } from '@/util/constants';

// Page title
export const PAGE_TITLE = 'EtherMium.com - decentralized Ethereum exchange';

// Backend URL
export const BACKEND_URL = process.env.PROD_ENV === 'main'
  ? 'https://b.ethermium.com'
  : 'https://backend.ethermium.devify.net';

/**
 * URL Ethereum Node, с которым взаимодействует приложение во время работы по Private Key.
 *
 * @type {string}
 */
export const ETHEREUM_NODE_HTTP = process.env.PROD_ENV === 'main'
  ? 'https://geth.ethermium.com/'
  : 'https://rinkeby.infura.io/v3/46ba637b8dfd47b2be420571e0249455';

// /**
//  * URL Ethereum Node, с которым взаимодействует приложение во время работы по Private Key.
//  *
//  * @type {string}
//  */
// export const ETHEREUM_NODE_WS = process.env.PROD_ENV === 'main'
//   ? 'wss://mainnet.infura.io/ws'
//   : 'wss://rinkeby.infura.io/ws';

// URL блокчейн сети
export const ETHERSCAN_URL = process.env.PROD_ENV === 'main'
  ? 'https://etherscan.io'
  : 'https://rinkeby.etherscan.io';

export const APP_ETH_NETWORK_ID = process.env.PROD_ENV === 'main'
  ? MAIN_NETWORK_ID
  : TEST_NETWORK_ID;

/**
 * Версия приложения
 */
export const APP_VERSION = process.env.APP_VERSION;

/**
 * Дата сборки приложения (фронтенда)
 */
export const APP_BUILD_DATE = process.env.APP_BUILD_DATE;

/**
 * Указывает в какой сети Ethereum работает приложение
 * main - главная сеть
 * test - тестовая сеть Rinkeby
 * @type {string}
 */
export const APP_ENV = process.env.PROD_ENV;

/**
 * Максимальное количество запросов которые агрегируются в одном batch запросе к Ethereum Node (e.g. Infura).
 * @type {number}
 */
export const BATCH_CHUNK_SIZE = 150;

// настройки связанные с комиссиями, затратами на gas и прочими финансовыми вопросами
export * from './currency';

// время
export * from './time';

// paging

/**
 * Возвращает количество оредров который нужно отображать на одной странице
 */
export const ORDERS_PAGE_SIZE = 20;
export const HISTORY_PAGE_SIZE = 20;
export const HISTORY_RECENT_SIZE = 5;
export const TOKEN_CATALOGUE_PAGE_SIZE = 20;
export const PAGE_SIZE_TRADES = 150;

/**
 * Число новостей для отображения
 * @type {number}
 */
export const NEWS_PAGE_SIZE = 20;

/**
 * Количество ордеров (в данном случае это ордера сгруппированные по цене), которые будут показываться в ордербуке по каждому типу (покупка, продажа)
 *
 * @type {number}
 */
export const ORDERS_IN_ORDERBOOK = 30;



