const state = {
  /**
   * Массив содержащий все цены на токен
   */
  candles: [],


  /**
   * Флаг, устанавликает в true когда цены загружаются и в false когда они загружены
   */
  candlesLoading: false,

  /**
   * Дата, начная с которой рисуется график
   */
  activePeriod: 0,

  /**
   * Буфер для точек графика, в который они добавляются если в данный момент загружаются свечки
   */
  pricesBuffer: [],

  /**
   * Длинна інтервала
   */
  interval: null,
};

export default state;



